<template>
  <div class="page-title">
    <div class="container">
      <div class="row">
        <div class="col-5">
          <h1>{{routeName}}</h1>
        </div>
        <div class="col-7">
          <nav>
            <ol class="breadcrumb">
              <li class="breadcrumb-item">
              <router-link replace exact :to="{name: 'overview'}">
              <i class="icon-home" /> Home
              </router-link>
              </li>
              <li class="breadcrumb-item bi" v-for="(breadcrumb, inx) in breadcrumbs" :key="inx" @click="routeTo(inx)" :class="{'linked':!!breadcrumb.link}">
              {{breadcrumb.name}}
              </li>
            </ol>
          </nav>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      breadcrumbs: Array
    }
  },
  mounted () {
    this.updateList()
  },
  computed: {
    routeName () {
      const { name } = this.$route
      var joined = ''
      var res = name.split('-')
      if (res.length > 1) {
        res.forEach(element => {
          joined += ' ' + element
        })
      } else joined = name
      return this.$capitalizeFirstLetter(joined.trim())
    }
  },
  watch: {
    $route () {
      this.updateList()
    }
  },
  methods: {
    updateList () {
      this.breadcrumbs = this.$route.meta.breadcrumb
    },
    routeTo (to) {
      if (this.breadcrumbs[to].link) {
        this.$router.replace({ name: this.breadcrumbs[to].link })
      }
    }
  }
}
</script>
<style>
.linked{cursor: pointer;}
</style>




