<template>
    <div :class='styling' @click='submit'>
        <i v-if='loading' class='icon-refresh pulse'/>
        <i v-if='displayIcon' :class='icon' />
        {{title}}
    </div>
</template>
<script>
export default {
  props: {
    actions: Array,
    icon: {
      type: String,
      default: 'icon-check-circle'
    },
    title: {
      type: String,
      default: 'Save'
    },
    styling: {
      type: String,
      default: 'btn btn-primary'
    },
    loading: false
  },
  computed: {
    displayIcon () {
      if (this.icon && !this.loading) {
        return true
      }
      return false
    }
  },
  methods: {
    submit (activity) {
      this.$emit('submit', this.item, activity.path)
    }
  }
}
</script>
<style scoped>
</style>





