<template>
    <div class='social-timeline-left'>
        <div class='card-body'>
            <div class='profile'>
                <img v-if='url' class='img-responsive img-responsive img-thumbnail' :src='src'>
            </div>
                <span v-if='!url' class='text-center logo-text img-thumbnail'>Logo not set</span>
            <div class='card-body '>
                <div>
                    <div class='form-group'>
                        <input class='form-control' type='file'  @change='fileChanged'>
                    </div>

                    <submit-button v-if='logo'
                    icon='icon-upload' 
                    styling='btn btn-primary btn-block' 
                    title='Upload' 
                    :loading='submitting' 
                    v-on:submit='upload'></submit-button>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
  data () {
    return {
      logo: null,
      submitting: false
    }
  },
  props: {
    url: null
  },
  computed: {
    src () {
      return this.url
    }
  },
  methods: {
    upload () {
      this.submitting = true
      const formData = new FormData()
      formData.append('image', this.logo, this.logo.name)
      this.$http
        .post(`settings/logo`, formData, {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        })
        .then(response => {
          this.submitting = false
          if (response.data.success) {
            this.url = response.data.data
            var settings = JSON.parse(localStorage.getItem('settings'))
            settings.logo = this.url
            localStorage.setItem('settings', null)
            localStorage.setItem('settings', JSON.stringify(settings))

            this.$toastr('success', 'Success')
            window.location.reload()
          } else {
            this.$toastr('error', response.data.message)
          }
        })
        .catch(e => {
          this.submitting = false
          this.$toastr('error', 'Failed')
        })
    },
    fileChanged (event) {
      this.logo = event.target.files[0]
    }
  }
}
</script>

<style scoped>
.logo-text {
  font-size: 2em;
  text-align: center;
}
.profile{
  width:100%;
}
</style>



