<template>
<div class="container-scroller">
    <div class="container-fluid page-body-wrapper full-page-wrapper">
        <div class="content-wrapper auth p-0 theme-two">
            <div class="row d-flex align-items-stretch">
                <div class="col-md-4 banner-section d-none d-md-flex align-items-stretch justify-content-center">
                    <div class="slide-content bg-2">
                    </div>
                </div>
                <div class="col-12 col-md-8 h-100 bg-white">
                    <div class="auto-form-wrapper d-flex align-items-center justify-content-center flex-column">
                        <div class="nav-get-started">
                            <p>Already have an account?</p>
                            <router-link class="btn get-started-btn" to='/login'>Log in here</router-link>
                        </div>
                        <form action="#">
                          <div class="text-center">
                          <img src="static/images/logo.png" alt="logo.png">
                          </div>
                            <h3 class="mr-auto">&nbsp;</h3>
                            <p class="mb-5 mr-auto">Sign Up.</p>
                            <div class='row'>
                                <div class='col-sm-6'>
                                    <div class="form-group">
                                        <div class="input-group">
                                            <div class="input-group-prepend">
                                                <span class="input-group-text"><i class="icon-lock"></i></span>
                                            </div>
                                            <input type="text" v-model='credentials.firstName' class="form-control" placeholder="First Name">
                                        </div>
                                    </div>
                                </div>
                                <div class='col-sm-6'>
                                    <div class="form-group">
                                        <div class="input-group">
                                            <div class="input-group-prepend">
                                                <span class="input-group-text"><i class="icon-lock"></i></span>
                                            </div>
                                            <input type="text" v-model='credentials.lastName' class="form-control" placeholder="Last Name">
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="form-group">
                                <div class="input-group">
                                    <div class="input-group-prepend">
                                        <span class="input-group-text"><i class="icon-envelope-letter"></i></span>
                                    </div>
                                    <input type='email' v-model='credentials.email' class="form-control" placeholder="Email Address">
                                </div>
                            </div>
                            <div class='row'>
                                <div class='col-sm-6'>
                                    <div class="form-group">
                                        <div class="input-group">
                                            <div class="input-group-prepend">
                                                <span class="input-group-text"><i class="icon-lock"></i></span>
                                            </div>
                                            <input type="password" v-model='credentials.password' class="form-control" placeholder="Password">
                                        </div>
                                    </div>
                                </div>
                                <div class='col-sm-6'>
                                    <div class="form-group">
                                        <div class="input-group">
                                            <div class="input-group-prepend">
                                                <span class="input-group-text"><i class="icon-lock"></i></span>
                                            </div>
                                            <input type="password" v-model='cpassword' class="form-control" placeholder="Confirm Password">
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div class="form-group">
                                <submit-button 
                                v-on:submit='submit' 
                                :loading='submitting' title='Sign up' 
                                icon='icon-pencil' class='btn btn-primary submit-btn'></submit-button>

                            </div>
                            <div class="wrapper mt-5 text-gray">
                                <p class="footer-text">Copyright © 2018 . All rights reserved.</p>
                  <!--               <ul class="auth-footer text-gray">
                                    <li><a href="#">Terms & Conditions</a></li>
                                    <li><a href="#">Cookie Policy</a></li>
                                </ul> -->
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>
<script>
import firebase from 'firebase'

export default {
  data () {
    return {
      submitting: false,
      cpassword: '',
      credentials: {
        email: '',
        password: '',
        firstName: '',
        lastName: ''
      },
      startYear: 0
    }
  },
  created () {
    let currentYear = new Date().getFullYear()
    this.startYear = currentYear - 118
  },
  methods: {
    submit () {
      this.submitting = true
      var credentials = {
        email: this.credentials.email,
        firstName: this.credentials.firstName,
        lastName: this.credentials.lastName,
        type: 'member'
      }
      firebase
        .auth()
        .createUserWithEmailAndPassword(
          this.credentials.email,
          this.credentials.password
        )
        .then(
          result => {
            this.submitting = false
            firebase
              .database()
              .ref('users/' + result.user.uid)
              .set(credentials)
            var user = firebase.auth().currentUser
            user
              .updateProfile({
                displayName: credentials.firstName + ' ' + credentials.lastName
              })
              .then(function () {
                console.log()
              })
            this.$toastr('success', 'Sign Up Successful')
            this.$router.replace('login')
          },
          err => {
            this.submitting = false
            this.$toastr('error', err.message)
          }
        )
    }
  },
  computed: {
    // ...mapState({
    //   errors: state => state.auth.errors
    // })
  }
}
</script>



