<template>
  <div class='row'>
    <div class='col-md-12'>
      <div class='card'>
           <data-table :loading='loading' v-on:buttonEvent='actionHandler' v-on:event='pages' :actions='actions' :resource='resource' :pagination='pagination' :title='title' :sub-title='subTitle' :data='data' :columns='columns'>
        </data-table>
      </div>
    </div>
  </div>
</template>
<script>
import { database } from '../../store/settings.module'
const usersRef = database.ref('users')

const resource = 'users'
export default {
  components: {
  },
  data  () {
    return {
      pagination: {},
      resource: resource,
      loading: true,
      notifications: {},
      title: 'Users',
      subTitle: '',
      actions: [
        {
          name: '',
          icon: 'icon-pencil',
          type: 'link',
          path: 'edit-user',
          design: 'success'
        },
        {
          name: '',
          type: 'button',
          icon: 'icon-trash',
          path: 'delete',
          design: 'danger'
        }
      ],
      columns: [
        {
          name: 'Name',
          type: ''
        },
        {
          name: 'Email',
          type: ''
        },
        {
          name: 'Type',
          type: 'badge'
        }
      ],
      data: []
    }
  },
  created  () {
    this.pages(10, 0)
    usersRef.on('child_added', snapshot => {
      console.log('user was added: ', { ...snapshot.val(), id: snapshot.key })
    })
    usersRef.on('child_removed', snapshot => {
      const deletedNote = this.data.find(user => user.id === snapshot.key)
      this.$toastr('Success', 'user was removed: ', deletedNote)
      const index = this.data.indexOf(deletedNote)
      this.data.splice(index, 1)
      this.index = this.index === 0 ? 0 : index - 1
    })
    usersRef.on('child_changed', snapshot => {
      const updatedNote = this.data.find(user => user.id === snapshot.key)
      updatedNote.title = snapshot.val().title
      updatedNote.content = snapshot.val().content
      console.log('user was updated: ', updatedNote)
    })
  },
  methods: {
    actionHandler: function (item, action) {
      switch (action) {
        case 'delete':
          usersRef.child(item.id).remove()
          break

        default:
          break
      }
    },
    pages: function (itemsPerPage, pageNumber) {
      this.loading = true
      const data = []
      usersRef.once('value', users => {
        users.forEach(user => {
          data.push({
            id: user.ref.key,
            reference: user.ref.key,
            name: user.child('firstName').val() + ' ' + user.child('lastName').val(),
            email: user.child('email').val(),
            type: user.child('type').val()
          })
        })
        this.data = data.filter(function (user) {
          return user.type !== 'member'
        })
        this.loading = false
      })
    }
  }
}
</script>
<style>

</style>



