<template>
    <div class="card">
        <div  class="card-body">
            <wysiwyg v-if="edit" style="background: #fff" v-model="terms" /> 
            <button class="pull-right btn btn-xs btn-primary" v-if="!edit" @click="edit = !edit">Edit</button>
            <div v-if="!edit" v-html="terms"></div> 
            <submit-button v-if="edit" :loading='false' v-on:submit="save()" title="Finish" icon="icon-check-circle" class="btn btn-primary pull-right"></submit-button>
        </div>
    </div>
</template>
<script>
import firebase from 'firebase'

export default {
  data () {
    return {
      terms: '',
      edit: false
    }
  },
  created () {
    firebase.database().ref('terms').on('value', (snapshot) => {
      this.terms = snapshot.val()
    })
  },
  methods: {
    save () {
      this.edit = false
      firebase.database().ref('terms').set(this.terms)
    }
  }
}
</script>



