<template>
    <div>
      <div v-if="showForm">
        <h5>Add child</h5>
        <div class="row">
          <div class="col-md-4">
            <fg-input type="text" autocomplete='off' label="First Name *" place-holder="First Name" v-model="child.firstName" />
          </div>
          <div class="col-md-4">
            <fg-input type="text" autocomplete='off' label="Middle Name *" place-holder="Middle Name" v-model="child.middleName" />
          </div>
          <div class="col-md-4">
            <fg-input type="text" autocomplete='off' label="Last Name *" place-holder="Last Name" v-model="child.lastName" />
          </div>
          <div class="col-md-6">
            <div class="form-group">
              <label>Gender *</label>
              <select v-model="child.gender" class="form-control">
                <option value="Male">Male</option>
                <option value="Female">Female</option>
              </select>
            </div>
          </div>
          <div class="col-md-6">
            <div class="form-group">
                <label>
                    Date of Birth *
                </label>
                <datetime type="date" @input="checkAge" input-class="form-control" v-model="child.dateOfBirth"></datetime>
            </div> 
          </div>
        <a v-if="showForm" class="btn btn-outline-primary btn-sm" @click="add()" >Add to list</a>

        </div>

       
      </div>
      <div v-if="!showForm" >
        <div class="card">
								<div class="card-body">
									<h5 class="card-title">Children</h5>
									<div class="table-responsive">
                    <table class="table">
                      <thead>
                        <tr>
                          <th class="pt-1 pl-0">
                            Name
                          </th>
                          <th class="pt-1">
                            Gender
                          </th>
                          <th class="pt-1">
                            Date Of Birth
                          </th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr v-for="(child, ch) in children" :key="ch">
                          <td class="py-1 pl-0">
                            <div class="d-flex align-items-center">
                              <div class="ml-3">
                                <p class="mb-2">{{ch+1}}. {{child.firstName + ' ' + child.middleName + ' ' + child.lastName }}</p>
                              </div>
                            </div>
                          </td>
                          <td>
                            <label class="badge badge-success">{{child.gender }}</label>
                          </td>
                          <td>
                            {{child.dateOfBirth | date}}
                          </td>
                        </tr>
                      </tbody>
                    </table>
                  <a class="btn btn-outline-primary pull-right btn-sm" @click="addChild()" >Add Child</a>
                </div>
							</div>
						</div>
      </div>
    </div>
</template>
<script>
export default {
  data () {
    return {
      child: {},
      showForm: {},
      children: []
    }
  },
  created () {
    this.init()
  },
  methods: {
    add () {
      const formData = JSON.parse(localStorage.getItem('form'))
      this.children.push(this.child)
      formData.children = this.children
      localStorage.setItem('form', JSON.stringify(formData))
      this.child = {}
      this.addChild()
    },
    init () {
      const formData = JSON.parse(localStorage.getItem('form'))
      if (formData.children && formData.children.length > 0) {
        this.children = formData.children
        this.showForm = false
      } else {
        this.showForm = true
      }
    },
    checkAge () {
      if (!this.child.dateOfBirth) return
      let age = this.$returnAge(this.child.dateOfBirth)
      if (age >= 18) {
        this.$toastr('error', 'Your child is eligible for their own cover')
        this.child.dateOfBirth = null
      }
    },
    addChild () {
      if (this.children.length >= 1) {
        this.$swal({
          title: '(' + this.children.length + ') children added. Would you like to add another?',
          text: 'Adding more than 2 children will increase your premium amount',
          type: 'info',
          showCancelButton: true,
          confirmButtonColor: '#3085d6',
          cancelButtonColor: '#d33',
          confirmButtonText: 'Yes, please!',
          cancelButtonText: 'Save and Close'
        }).then((result) => {
          if (result.value) {
            this.showForm = true
          } else {
            this.showForm = false
          }
        })
      } else {
        this.showForm = true
      }
    }
  }
}
</script>



