import Vue from 'vue'
import Router from 'vue-router'
import NotFound from '@/components/GeneralViews/NotFoundPage'

import Login from '@/views/Register/Login'
import SignUp from '@/views/Register/SignUp'

import DashboardLayout from '@/components/Layout/DashboardLayout'

import Overview from '@/views/Dashboards/Overview'
import FillForm from '@/views/Form/Fill'
import Terms from '@/views/Form/Terms'
import Review from '@/views/Form/Review'
import Subscriptions from '@/views/Subscription/List'
import EditSubscriptions from '@/views/Subscription/Edit'
import Timeline from '@/views/Document/Timeline'

import Users from '@/views/User/List'
import Settings from '@/views/Settings/Settings'
import Branding from '@/views/Settings/Branding'
import Profile from '@/views/Settings/Profile'
import EditTerms from '@/views/Settings/Terms'
import SetupAccount from '@/views/Settings/Create'

import EditUsers from '@/views/User/Edit'
import Subscribe from '@/views/Payment/Subscribe'
import Execute from '@/views/Payment/Execute'
import Payments from '@/views/Payment/List'
import EditPayment from '@/views/Payment/Edit'
import Search from '@/views/Search'
import firebase from 'firebase'
import '../../node_modules/nprogress/nprogress.css'
import NProgress from 'nprogress'

Vue.use(Router)
let router = new Router({
  routes: [
    {
      path: '/login',
      component: Login,
      name: 'login'
    },
    {
      path: '/sign-up',
      component: SignUp,
      name: 'sign-up'
    },
    {
      path: '/',
      component: DashboardLayout,
      redirect: '/login',
      name: 'home',
      children: [
        {
          path: 'overview',
          name: 'overview',
          component: Overview,
          meta: {
            requiresAuth: true
          }
        },
        {
          path: 'subscriptions',
          name: 'subscriptions',
          component: Subscriptions,
          meta: {
            breadcrumb: [
              { name: 'subscriptions' }
            ]
          }
        },
        {
          path: 'subscriptions/edit/:id',
          name: 'edit-client',
          component: EditSubscriptions,
          meta: {
            breadcrumb: [
              { name: 'subscriptions', link: 'subscriptions' },
              { name: 'Edit Subscription' }
            ]
          }
        },
        {
          path: 'subscriptions/view-timeline/:id',
          name: 'view-timeline',
          component: Timeline,
          meta: {
            breadcrumb: [
              { name: 'subscriptions', link: 'subscriptions' },
              { name: 'Timeline' }
            ]
          }
        },
        {
          path: 'subscriptions/add',
          name: 'add-subscriptions',
          component: EditSubscriptions,
          meta: {
            breadcrumb: [
              { name: 'subscriptions', link: 'subscriptions' },
              { name: 'Add subscriptions' }
            ]
          }
        },
        {
          path: 'fill-form/:category',
          name: 'fill-form',
          component: FillForm,
          meta: {
            breadcrumb: [
              { name: 'Fill form' }
            ]
          }
        },
        {
          path: 'terms-and-conditions/:category',
          name: 'terms-and-conditions',
          component: Terms,
          meta: {
            breadcrumb: [
              { name: 'Terms And Conditions' }
            ]
          }
        },
        {
          path: 'terms/edit',
          name: 'edit-terms',
          component: EditTerms,
          meta: {
            breadcrumb: [
              { name: 'Terms And Conditions' }
            ]
          }
        },
        {
          path: 'details/:id',
          name: 'subscription-detail',
          component: Review,
          meta: {
            breadcrumb: [
              { name: 'Review' }
            ]
          }
        },
        {
          path: 'subscription-execute',
          name: 'subscription-execute',
          component: Execute,
          meta: {
            breadcrumb: [
              { name: 'Execute' }
            ]
          }
        },
        {
          path: 'review',
          name: 'review',
          component: Review,
          meta: {
            breadcrumb: [
              { name: 'Review' }
            ]
          }
        },
        {
          path: 'profile',
          name: 'profile',
          component: Review,
          meta: {
            breadcrumb: [
              { name: 'Profile' }
            ]
          }
        },
        {
          path: 'payments',
          name: 'payments',
          component: Payments,
          meta: {
            breadcrumb: [
              { name: 'Payments' }
            ]
          }
        },
        {
          path: 'add-payment',
          name: 'add-payment',
          component: EditPayment,
          meta: {
            breadcrumb: [
              { name: 'Payments' }
            ]
          }
        },
        {
          path: 'subscribe',
          name: 'subscribe',
          component: Subscribe,
          meta: {
            breadcrumb: [
              { name: 'Subscribe to Payments' }
            ]
          }
        },
        {
          path: 'users',
          name: 'users',
          component: Users,
          meta: {
            breadcrumb: [
              { name: 'Users' }
            ]
          }
        },
        {
          path: 'users/edit/:id',
          name: 'edit-user',
          component: EditUsers,
          meta: {
            breadcrumb: [
              { name: 'Users', link: 'users' },
              { name: 'Edit User' }
            ]
          }
        },
        {
          path: 'users/add',
          name: 'add-users',
          component: EditUsers,
          meta: {
            breadcrumb: [
              { name: 'Users', link: 'users' },
              { name: 'Add User' }
            ]
          }
        },
        {
          path: 'search/:searchTerm',
          name: 'search',
          component: Search
        },
        {
          path: 'settings',
          name: 'settings',
          component: Settings,
          redirect: '/settings/branding',
          children: [
            {
              path: 'branding',
              name: 'branding',
              component: Branding,
              meta: {
                breadcrumb: [
                  { name: 'Settings', link: 'settings' },
                  { name: 'Branding' }
                ]
              }
            },
            {
              path: 'profile',
              name: 'profile',
              component: Profile,
              meta: {
                breadcrumb: [
                  { name: 'Settings', link: 'settings' },
                  { name: 'Profile' }
                ]
              }
            }
          ]
        }
      ]
    },
    // Handle child routes with a default, by giving the name to the
    // child.
    // SO: https://github.com/vuejs/vue-router/issues/777
    // {
    //   path: '/@:username',
    //   component: Profile,
    //   children: [
    //     {
    //       path: '',
    //       name: 'profile',
    //       component: ProfileArticles
    //     },
    //     {
    //       name: 'profile-favorites',
    //       path: 'favorites',
    //       component: ProfileFavorited
    //     }
    //   ]
    // },
    {
      path: 'setup-account',
      name: 'setup-account',
      component: SetupAccount
    },
    { path: '*', component: NotFound }

  ]
})

router.beforeEach((to, from, next) => {
  let currentUser = firebase.auth().currentUser
  let requiresAuth = to.matched.some(record => record.meta.requiresAuth)
  if (requiresAuth && !currentUser) {
    next('login')
  } else if (!requiresAuth && currentUser) {
    next()
  } else {
    next()
  }
})

router.beforeResolve((to, from, next) => {
  if (to.name) {
    NProgress.start()
  }
  next()
})

router.afterEach(() => {
  NProgress.done()
})

export default router



