// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from '@/router'
import DateFilter from '@/common/date.filter'
import ErrorFilter from '@/common/error.filter'
import CurrencyFilter from '@/common/currency.filter'
import Paginate from 'vuejs-paginate'
import GlobalComponents from './globalComponents'
import SideBar from '@/components/UIComponents/SidebarPlugin'
import VueToastr from '@deveodk/vue-toastr'
import MethodsPlugin from '@/helpers/methods'
import Categories from '@/helpers/categories'
import axios from 'axios'
import firebase from 'firebase'
import { database } from './store/settings.module'
import VueSweetalert2 from 'vue-sweetalert2'
import VueTelInput from 'vue-tel-input'
import Datetime from 'vue-datetime'
import wysiwyg from 'vue-wysiwyg'

import '@deveodk/vue-toastr/dist/@deveodk/vue-toastr.css'
import 'vue-tel-input/dist/vue-tel-input.css'
import 'vue-datetime/dist/vue-datetime.css'
import 'vue-wysiwyg/dist/vueWysiwyg.css'
if (database) {
  console.log('database ready')
}

window.clientId = 'AReVs0yJ0E9PAUMDQ5f6t1U20keya7HD6Yc-skQN8txbE4guj05uFnG6IsvjBJ0ovPBWJFpRl-YRi6bK'
window.secret = 'EP3WtOwQP1pdq90g2qn9l4yNPHljgDa0-Ip5aLKgpp80tUQMcoRA8oipxxZsGtxjbxd362HitsGmM8vv'

function getBaseUrl () {
  let baseUrl = window.location.protocol + '//' + window.location.host + '/' + window.location.pathname
  let hostUrl = baseUrl.replace(/^((\w+:)?\/\/[^/]+\/?).*$/, '$1')
  hostUrl = hostUrl + 'awa/'
  window.hostUrl = hostUrl
}

getBaseUrl()

Vue.config.productionTip = false
Vue.use(GlobalComponents)
Vue.use(Datetime)
Vue.use(MethodsPlugin)
Vue.use(SideBar)
Vue.use(Categories)
Vue.use(VueSweetalert2)
Vue.use(VueToastr, {
  defaultPosition: 'toast-top-right',
  defaultType: 'info',
  defaultTimeout: 3000
})
Vue.use(VueTelInput)
Vue.use(wysiwyg, {})
Vue.filter('date', DateFilter)
Vue.filter('error', ErrorFilter)
Vue.filter('currency', CurrencyFilter)
Vue.component('paginate', Paginate)

let app

export const apiUtils = axios.create()
Vue.prototype.$http = apiUtils

apiUtils.interceptors.request.use(function (config) {
  var user = JSON.parse(localStorage.getItem('user'))
  if (user) {
    // var token = user.api_token
    // config.headers.Authorization = `Bearer ${token}`
    return config
  } else {
    return config
  }
}, function (err) {
  return Promise.reject(err)
})

apiUtils.interceptors.response.use(undefined, err => {
  var data = {}
  if (typeof err.response !== 'undefined') {
    switch (err.response.status) {
      case 401:
        data.message = err.response.status + ' - ' + 'You are not authorised. Please log in again'
        break

      case 422:
        var errors = err.response.data.errors
        data.message = 'You have validation errors'
        data.details = errors
        break

      case 424:
        data.message = err.response.data.message
        break

      case 404:
        data.message = err.response.status + ' - ' + 'Seems your connection is lost'
        break
      case 405:
        data.message = err.response.status + ' - ' + 'The method used is not allowed'
        break

      default:
        if (typeof err.response.data === 'object') {
          data.message = err.response.status + ' ' + err.response.data.err || err.response.data.message
        } else {
          data.message = 'Something went wrong. Please try again.'
        }
        break
    }
  } else {
    data.message = 'An error occured attempting to execute the request. Please check your internet connection'
  }
  return Promise.reject(data)
}, function (err) {
  return Promise.reject(err)
})
firebase.auth().onAuthStateChanged(function (user) {
  if (!app) {
    app = new Vue(
      {
        el: '#app',
        router,
        components: { App },
        template: '<App/>'
      }
    )
  }
})



