<template>
    <div>
        <member v-if="currentUser.type == 'member'"></member>
        <admin v-if="currentUser.type == 'admin'"></admin>
        <affiliate v-if="currentUser.type == 'affiliate'"></affiliate>
    </div>
</template>
<script>
import Member from './Member.vue'
import Admin from './Admin.vue'
import Affiliate from './Affiliate.vue'

export default {
  components: {
    Member,
    Admin,
    Affiliate
  },
  data () {
    return {
    }
  },
  computed: {
    currentUser () {
      return JSON.parse(localStorage.getItem('user'))
    }
  },
  created () {
  },
  mounted () {
  },
  methods: {

  }
}
</script>
<style>
.card .numbers {
    font-size: 2em;
    text-align: right;
}
p {
    font-size: 16px;
    line-height: 1.4em;
}

.card .icon-big {
    font-size: 2.5em;
    min-height: 64px;
}
.stats{font-size: 1em; font-weight: 100;}
</style>



