<template>
<div class='tab-pane active' role='tabpanel'>
    <div class='card'>
        <div class='card-body'>
            <h5 class='card-title'>Branding</h5>
        </div>
        <div class='card-body'>
            <div class='row'>
                <div class='col-xl-3 col-lg-4 col-md-4 col-xs-12'>
                      <logo-section :url='logoImageUrl'></logo-section>
                </div>
                <div class='col-xl-9 col-lg-8 col-md-8 col-xs-12 card '>
                    <h3>Details</h3>
                    <fg-input 
                    type='text'
                    label='Name' 
                    v-model='settings.name' 
                    placeholder='Name' 
                    />
                    <div class='form-group'>
                        <label for=''>Address</label>
                        <textarea placeholder='Address'  v-model='settings.address' cols='30' class='form-control' rows='5'></textarea>
                    </div>

                    <fg-input 
                    type='text'
                    label='Phone Number' 
                    v-model='settings.phone' 
                    placeholder='Phone Number' 
                    />

                    <fg-input 
                    type='email'
                    label='Email Address' 
                    v-model='settings.email' 
                    placeholder='Email Address' 
                    />

                    <fg-input 
                    type='url'
                    label='Website' 
                    v-model='settings.website' 
                    placeholder='Website' 
                    />

                    <div class='form-group'>
                        <label for=''>Theme Colour</label><br>
                        <input v-model='settings.primary_color' type='color'>
                    </div>

                    <div class='form-group'>
                        <label for=''>Secondary Colour</label><br>
                        <input v-model='settings.secondary_color' type='color'>
                    </div>

                    <fg-input 
                    type='text'
                    label='Tagline' 
                    v-model='settings.tagline' 
                    placeholder='Tagline' 
                    />

                    <fg-input 
                    type='text'
                    label='Document Slogan' 
                    v-model='settings.document_slogan' 
                    placeholder='Document Slogan' 
                    />
                    
                    <fg-input 
                    type='number'
                    label='Tax Rate % ' 
                    v-model='settings.tax' 
                    placeholder='Tax Rate % ' 
                    />
                </div>
             
              
            </div>
        </div>
        <div class='card-footer'>
            <div class='pull-right'>
             <button class='btn btn-default' >Cancel</button>
             <submit-button :loading='submitting' v-on:submit='save'></submit-button>
            </div>
        </div>
    

    </div>
</div>
    
</template>
<script>
import LogoSection from './Logo'
export default {
  data () {
    return {
      settings: {
        tax: 0,
        primary_color: '#800800',
        secondary_color: '#FF0021',
        logo: 'logo not set'
      },
      logo: null,
      setLogo: false,
      submitting: false
    }
  },
  components: {
    LogoSection
  },
  computed: {
    logoImageUrl () {
      if (this.settings) return this.settings.logo
      return null
    }
  },
  methods: {
    fetch () {
      this.$http
        .get('settings')
        .then(result => {
          if (result.data.data) {
            this.settings = result.data.data
            this.setLogo = true
          }
        })
        .catch(error => {
          this.$toastr('error', error)
        })
    },
    save () {
      this.submitting = true
      this.$http
        .post('settings', this.settings)
        .then(response => {
          if (response.data.success) {
            this.submitting = false
            localStorage.setItem('settings', null)
            localStorage.setItem('settings', JSON.stringify(this.settings))
            window.location.reload()
          } else {
            this.$toastr('error', response.data.message)
          }
        })
        .catch(e => {
          this.submitting = false
          this.$toastr('error', 'Failed')
        })
    }
  },
  created () {
    this.fetch()
  }
}
</script>
<style>
</style>





