<template>
  <div class='card'>
    <div class='card-body'>
      <h4 class='card-title'>{{pageTitle}}</h4>
    </div>
    <div class='card-body'>
      <form>
        <div class='row'>
          <div class='col-md-12'>
            <fg-input type='text'
                      label='First Name'
                      placeholder='First Name'
                      v-model='user.firstName'>
            </fg-input>

            <fg-input type='text'
                      label='Last Name'
                      placeholder='Last Name'
                      v-model='user.lastName'>
            </fg-input>

            <fg-input type='email'
                      label='Email'
                      placeholder='Email'
                      v-model='user.email'>
            </fg-input>

          <div class="form-group">
            <label>Type </label>
            <select v-model="user.type" class="form-control">
              <option value="admin">Admin</option>
              <option value="affiliate">Affiliate</option>
              <option value="member">Member</option>
            </select>
          </div>


           

          </div>
        </div>
 
        <div class='text-center'>
          <submit-button :loading='submitting' class='btn btn-info btn-fill btn-wd' v-on:submit='update' >
            {{buttonTitle}}
          </submit-button>
        </div>
        <div class='clearfix'></div>
      </form>
    </div>
  </div>
</template>
<script>
import firebase from 'firebase'
import { database } from '../../store/settings.module'
const usersRef = database.ref('users')

export default {
  data () {
    return {
      id: 0,
      edit: false,
      submitting: false,
      user: {
        type: 'admin',
        status: 0
      },
      newItem: {}
    }
  },
  computed: {
    pageTitle () {
      if (this.edit) return `Edit ${this.user.firstName}`
      return 'Add new user'
    },
    buttonTitle () {
      if (this.edit) return `Save changes`
      return 'Add New'
    }
  },
  created () {
    if (this.$route.params.id) {
      this.id = this.$route.params.id
      this.fetch()
      this.edit = true
    }
  },
  methods: {
    update () {
      this.submitting = true
      var user = {
        email: this.user.email,
        firstName: this.user.firstName,
        lastName: this.user.lastName,
        type: this.user.type
      }
      if (this.edit) {
        usersRef.child(this.$route.params.id).set(user)
        this.$toastr('success', 'Success')
        this.$router.push({name: 'users'})
        this.submitting = false
      } else {
        firebase.auth().createUserWithEmailAndPassword(this.user.email, '123456789').then(
          (result) => {
            firebase.database().ref('users/' + result.user.uid).set(user)
            this.sendEmail(result.user)
            this.$toastr('success', 'Success')
            this.submitting = false
            this.$router.push({name: 'users'})
          },
          (err) => {
            this.submitting = false
            this.$toastr('error', err.message)
          }
        )
      }
    },
    fetch () {
      usersRef.child(this.$route.params.id).on('value', (snapshot) => {
        this.user = snapshot.val()
      })
    },
    sendEmail (user) {
      user.sendEmailVerification().then(function () {
        // Email sent.
      }).catch(function (error) {
        // An error happened.
        console.log(error)
      })
    }
  }
}
</script>
<style>

</style>


