<template>
    <div class='row'>
        <div class='col-md-12'>
            <div class='card'>
                <div class='card-body'>
                    <h4 class="card-title">Filters</h4>
                </div>
                <div class='card-body'>
                    <h3>From</h3>
                    <div class='row'>
                        <div class='col-md-6'>
                            <select class='form-control' v-model="fyear">
                                <option v-for="(year,fy) in years" :key="fy">{{year}}</option>
                            </select>
                        </div>
                        <div class='col-md-6'>
                            <select class='form-control' v-model="fmonth">
                                <option v-for="(monthf,fm) in months" :key="fm" :value="monthf.id">{{monthf.name}}</option>
                            </select>
                        </div>
                    </div>
                    <hr>
                    <h3>To</h3>
                    <div class='row'>
                        <div class='col-md-6'>
                            <select class='form-control' v-model="tyear">
                                <option v-for="(yeart,ty) in years" :key="ty">{{yeart}}</option>
                            </select>
                        </div>
                        <div class='col-md-6'>
                            <select class='form-control' v-model="tmonth">
                                <option v-for="(montht,tm) in months" :key="tm" :value="montht.id">{{montht.name}}</option>
                            </select>
                        </div>
                    </div>
                </div>
              <div class="clearfix">&nbsp;
                  <hr>
              </div>
                <div class="card-body">
                  <data-table :canAdd="false" :loading='loading' v-on:buttonEvent='actionHandler' v-on:event='pages' :actions='actions' :resource='resource' :pagination='pagination' :title='title' :sub-title='subTitle' :data='data' :columns='columns'>
            </data-table>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { database } from '../../store/settings.module'
const paymentsRef = database.ref('paypal')
const usersRef = database.ref('users')

const resource = 'payments'
export default {
  components: {},
  data () {
    return {
      pagination: {},
      resource: resource,
      loading: true,
      title: 'Payments',
      subTitle: 'All the payments ',
      columns: [
        {
          name: 'Date Paid',
          type: 'code'
        },
        {
          name: 'Amount',
          type: 'currency'
        },
        // {
        //   name: 'From',
        //   type: ''
        // },
        // {
        //   name: 'User',
        //   type: ''
        // },
        {
          name: 'Status',
          type: ''
        }
      ],
      actions: [],
      data: [],
      fmonth: 'Jan',
      tmonth: 'Dec',
      fyear: '2018',
      tyear: '2018',
      years: ['2017', '2018', '2019'],
      months: [
        { id: 'Jan', name: 'January' },
        { id: 'Feb', name: 'February' },
        { id: 'Mar', name: 'March' },
        { id: 'Apr', name: 'April' },
        { id: 'May', name: 'May' },
        { id: 'Jun', name: 'June' },
        { id: 'Jul', name: 'July' },
        { id: 'Aug', name: 'August' },
        { id: 'Sep', name: 'September' },
        { id: 'Oct', name: 'October' },
        { id: 'Nov', name: 'November' },
        { id: 'Dec', name: 'December' }
      ]
    }
  },
  created  () {
    this.pages(10, 0)
    paymentsRef.on('child_added', snapshot => {
      console.log('payment was added: ', { ...snapshot.val(), id: snapshot.key })
    })
    paymentsRef.on('child_removed', snapshot => {
      const deletedNote = this.data.find(payment => payment.id === snapshot.key)
      this.$toastr('Success', 'payment was removed: ', deletedNote)
      const index = this.data.indexOf(deletedNote)
      this.data.splice(index, 1)
      this.index = this.index === 0 ? 0 : index - 1
    })
    paymentsRef.on('child_changed', snapshot => {
      const updatedNote = this.data.find(payment => payment.id === snapshot.key)
      updatedNote.title = snapshot.val().title
      updatedNote.content = snapshot.val().content
      console.log('payment was updated: ', updatedNote)
    })
  },
  methods: {
    pages: function (itemsPerPage, pageNumber) {
      this.loading = true
      const data = []
      paymentsRef.once('value', payments => {
        console.log(payments)
        payments.forEach(payment => {
          data.push({
            id: payment.ref.key,
            reference: payment.ref.key,
            datepaid: payment.child('create_time').val(),
            amount: payment.child('resource').child('amount').child('total').val(),
            from: payment.child('resource').child('invoice_number').val().split('-')[0],
            status: payment.child('resource').child('state').val()
          })
        })
        data.forEach(element => {
          usersRef.child(element.from).on('value', (snapshot) => {
            data.user = snapshot.val().firstName
            console.log(data.user)
          })
        })
        this.data = data
        this.loading = false
      })
    },
    fetch (id) {
      usersRef.child(id).on('value', (snapshot) => {
        return snapshot.val().firstName
      })
    },
    actionHandler: function (item, action) {
      switch (action) {
        case 'delete':
          this.$http
            .post(this.resource + '/' + action, item)
            .then(result => {
              this.$toastr('success', 'Success')
              this.pages(10, this.pagination.current)
            })
            .catch(error => {
              this.$toastr('error', error.message)
            })
          break

        default:
          break
      }
    }
  }
}
</script>
<style>
</style>



