<template>
  <div class="col-xl-12">
        <div class="card">
        <div class="card-header">
            <h5 class="card-header-text">{{pageTitle}}</h5>
        </div>
        <div class="card-body user-desc">
            <div class="view-desc">
                <div v-html="terms"></div>
                    <p>
                        By clicking continue, you agree to the above terms and conditions
                        <br>
                          <button @click="go()" class="btn btn-sm btn-outline-primary btn-primary">Continue
                    </button>
                    </p>
            </div>
        </div>
    </div>
  </div>
</template>
<script>
import firebase from 'firebase'
export default {
  data () {
    return {
      category: '',
      terms: ''
    }
  },
  computed: {
    pageTitle () {
      return `Terms and Conditions for the ${this.category} subscription`
    }
  },
  created () {
    this.category = this.$categories.categoryDisplayName(
      this.$route.params.category
    )
    firebase.database().ref('terms').on('value', (snapshot) => {
      this.terms = snapshot.val()
    })
  },
  methods: {
    go () {
      this.$router.push({
        name: 'fill-form',
        params: { category: this.$route.params.category }
      })
    }
  }
}
</script>




