<template>
<div class="row">
    <div @click="select(plan)" v-for="(plan, p) in plans" :key="p" class="col-md-6">
        <div class="card plan">
            <div class="card-body">
                <div class="row align-items-center">
                    <div class="col-8">
                        <h4 :class="(selectedPlan.value == plan.value) ? 'text-success':'text-primary'">
                            <span v-if="selectedPlan.value == plan.value" 
                        class="text-success m-b-0 text-right"><i class="icon-check"></i>
                        </span>
                        <span class="text-right">
                            $ {{plan.value}}
                            </span>
                            </h4>
                        
                    </div>
                    <div class="col-4 text-right">
                        <i class="feather icon-bar-chart-2 f-28"></i>
                    </div>
                </div>
            </div>
            <div :class="(selectedPlan.value == plan.value) ? 'card-footer bg-success':'card-footer bg-primary'" class="">
                <div class="row align-items-center">
                    <div class="col-9">
                        <p class="text-white m-b-0">{{plan.frequency}}</p>
                    </div>
                    <div class="col-3 text-right">
                        <i class="feather icon-trending-up text-white f-16"></i>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>
<script>
export default {
  data () {
    return {
      plans: [],
      selectedPlan: {}
    }
  },
  created () {
    this.init()
    this.getPlans()
  },
  computed: {
  },
  methods: {
    init () {
      const formData = JSON.parse(localStorage.getItem('form'))
      if (formData.plan) {
        this.selectedPlan = formData.plan
      }
    },
    getPlans () {
      const formData = JSON.parse(localStorage.getItem('form'))
      let category = null
      category = this.$categories.categoryDetails(formData.category)
      let monthlyAddition = 0
      let annualAddition = 0
      if (formData.children && formData.children.length > 2) {
        monthlyAddition = (formData.children.length - 2) * 8
        annualAddition = (formData.children.length - 2) * 70
      }
      var monthlyPayment = category.plans.filter(function (plan) {
        return plan.frequency === 'Monthly'
      })
      var annualPayment = category.plans.filter(function (plan) {
        return plan.frequency === 'Annually'
      })

      let monthly = {
        frequency: 'Monthly',
        value: monthlyPayment[0].value + monthlyAddition
      }
      let annually = {
        frequency: 'Annually',
        value: annualPayment[0].value + annualAddition
      }

      this.plans = []
      this.plans.push(monthly)
      this.plans.push(annually)
    },
    select (plan) {
      const formData = JSON.parse(localStorage.getItem('form'))
      formData.plan = plan
      this.selectedPlan = plan
      localStorage.setItem('form', JSON.stringify(formData))
    }
  }
}
</script>
<style>
.plan:hover {
  box-shadow: 3px 3px 8px #888888;
  cursor: pointer;
}
</style>



