<template>
  <div class='card'>
    <div class='card-header'>
      <h4 class='title'>{{pageTitle}}</h4>
    </div>
    <div class='card-body'>
      <div v-if="loading">
        <i  class='icon-refresh pulse'/>
        Finishing up on your plan ...
      </div>
    </div>
  </div>
</template>
<script>
import firebase from 'firebase'
export default {
  props: {
    id: null
  },
  data () {
    return {
      loading: false,
      pageTitle: ''
    }
  },
  created () {
    this.getAccessToken()
  },
  methods: {
    addSubscription (data) {
      this.submitting = true
      const user = JSON.parse(localStorage.getItem('user'))
      firebase.database().ref('agreements/' + user.uid).set(data)
    },
    getAccessToken () {
      let data = 'grant_type=client_credentials'
      this.$http.post('https://api.sandbox.paypal.com/v1/oauth2/token/', data,
        {
          headers: {
            'Accept': 'application/json',
            'Accept-Language': 'en_US',
            'Authorization': 'Basic ' + btoa('AReVs0yJ0E9PAUMDQ5f6t1U20keya7HD6Yc-skQN8txbE4guj05uFnG6IsvjBJ0ovPBWJFpRl-YRi6bK:EP3WtOwQP1pdq90g2qn9l4yNPHljgDa0-Ip5aLKgpp80tUQMcoRA8oipxxZsGtxjbxd362HitsGmM8vv')
          }
        })
        .then((response) => {
          this.accessToken = response.data.access_token
          this.pageTitle = 'Finishing up ...'
          this.init()
        })
        .catch((error) => {
          // An error happened.
          console.log(error)
        })
    },
    init () {
      this.loading = true
      this.$http.post('https://api.sandbox.paypal.com/v1/payments/billing-agreements/' + this.$route.query.token + '/agreement-execute', null,
        {
          headers: {
            'Content-type': 'application/json',
            'Authorization': 'Bearer ' + this.accessToken
          }
        })
        .then((response) => {
          this.loading = false
          this.addSubscription(response.data)
          this.$router.push({name: 'overview'})
          this.$toastr('success', 'Your plan is all set up')
        })
        .catch((error) => {
          this.loading = false
          this.pageTitle = error.message
        })
    }
  }
}
</script>


