import Sidebar from './SideBar.vue'

const SidebarStore = {
  showSidebar: true,
  displaySidebar (value) {
    this.showSidebar = value
  },
  getLinks (value) {
    var links = []
    switch (value.toLowerCase()) {
      case 'admin':
        links.push({
          name: 'Home',
          icon: 'menu-icon icon-home',
          path: '/overview'
        },
        {
          name: 'Subscriptions',
          icon: 'menu-icon icon-check',
          path: '/subscriptions'
        },
        {
          name: 'Payments',
          icon: 'menu-icon icon-envelope',
          path: '/payments'
        },
        {
          name: 'Users',
          icon: 'menu-icon icon-people',
          path: '/users'
        })
        break
      case 'member':
        links.push({
          name: 'Home',
          icon: 'menu-icon icon-home',
          path: '/overview'
        },
        {
          name: 'Profile',
          icon: 'menu-icon icon-user',
          path: '/profile'
        })
        break
      case 'affiliate':
        links.push({
          name: 'Home',
          icon: 'menu-icon icon-home',
          path: '/overview'
        },
        {
          name: 'My Subscriptions',
          icon: 'menu-icon icon-check',
          path: '/subscriptions'
        },
        {
          name: 'Profile',
          icon: 'menu-icon icon-user',
          path: '/profile'
        })
        break
      default:
        break
    }
    return links
  }
}

const SidebarPlugin = {

  install (Vue) {
    Vue.mixin({
      data () {
        return {
          sidebarStore: SidebarStore
        }
      }
    })

    Object.defineProperty(Vue.prototype, '$sidebar', {
      get () {
        return this.$root.sidebarStore
      }
    })
    Vue.component('side-bar', Sidebar)
  }
}

export default SidebarPlugin



