<template>
<div class="card mb-3">
        <div class="card-body">
          <h4 class='title'>{{pageTitle}}</h4>
          </div>
        <div class="card-body">
  <form>
        <div class='row'>
          <div class='col-md-12'>
            <fg-input type='text'
                      label='Name'
                      placeholder='Name'
                      v-model='client.name'>
            </fg-input>
           
            <fg-input type='email'
                      label='Email'
                      placeholder='Email'
                      v-model='client.email'>
            </fg-input>
            
            <fg-input type='text'
                      label='Phone Number'
                      placeholder='Phone Number'
                      v-model='client.phone'>
            </fg-input>
            
            <fg-input type='text'
                      label='Description'
                      placeholder='Description'
                      v-model='client.description'>
            </fg-input>
            
            <fg-input type='text'
                      label='Website'
                      placeholder='Website'
                      v-model='client.website'>
            </fg-input>

          </div>
        </div>
 
        <div class='text-center'>
          <button type='submit' class='btn btn-info btn-fill btn-wd' @click.prevent='update'>
            {{buttonTitle}}
          </button>
        </div>
        <div class='clearfix'></div>
      </form>
        </div>
        <div class="card-footer small text-muted">&nbsp;</div>
      </div>
 
</template>
<script>
export default {
  data () {
    return {
      id: 0,
      edit: false,
      client: {
        status: 0
      }
    }
  },
  computed: {
    pageTitle () {
      if (this.edit) return `Edit ${this.client.name}`
      return 'Add new client'
    },
    buttonTitle () {
      if (this.edit) return `Save changes`
      return 'Add New'
    }
  },
  created () {
    if (this.$route.params.id) {
      this.id = this.$route.params.id
      this.fetch()
      this.edit = true
    }
  },
  methods: {
    update () {
      var resource = this.edit ? '/update' : ''
      this.$http
        .post('subscriptions' + resource, this.client)
        .then(result => {
          this.$toastr('success', 'Success')
          this.$router.push('/subscriptions')
        })
        .catch(error => { this.$toastr('error', error.message) })
    },
    fetch () {
      this.$http
        .get('subscriptions/' + this.id)
        .then(result => { this.client = result.data.data })
        .catch(error => { this.$toastr('error', error.message) })
    }
  }
}
</script>
<style>

</style>



