<template>
    <div>
      <div v-if="spouseOption" >
        <a @click='set()' class="btn btn-sm btn-outline-primary"> 
          <span v-if="!setSpouse" class="icon-close"></span> 
          <span v-if="setSpouse" class="icon-check"></span> 
          Use spouse details
          </a>
          <br>
          <br>
      </div>

        <fg-input @input="store" type='text' place-holder='Full Name' label='Full Name *' v-model='form.fullname'/>                                 
        <div class="form-group">
            <label>
                Phone Number * 
            </label>
            <vue-tel-input :preferredCountries="['us', 'gb']" class="phone has-error" @input="store" autocomplete='off' v-model="form.phoneNumber"></vue-tel-input>
        </div>          
        <fg-input @input="store" type='email' place-holder='Email' label='Email *' v-model='form.email'/> 
        <fg-input @input="store" type='text' place-holder='Address' label='Address' v-model='form.address'/> 
                                    
    </div>
</template>
<script>
export default {
  data () {
    return {
      form: {
        fullname: '',
        address: ''
      },
      spouse: {},
      setSpouse: false,
      spouseOption: false
    }
  },
  created () {
    this.checkSpouse()
  },
  methods: {
    set () {
      const formData = JSON.parse(localStorage.getItem('form'))
      this.setSpouse = !this.setSpouse
      if (this.setSpouse) {
        this.form.fullname = formData.spouse.firstName + ' ' + formData.spouse.middleName + ' ' + formData.spouse.lastName
        this.form.address = formData.spouse.physicalAddress
        this.store()
      } else {
        this.form = {}
        this.store()
      }
    },
    checkSpouse () {
      const formData = JSON.parse(localStorage.getItem('form'))
      if (formData.kin === null || typeof formData.kin === 'undefined') {
        if (formData.spouse === null || typeof formData.spouse === 'undefined') {
          this.spouseOption = false
        } else {
          this.spouseOption = true
        }
      } else {
        this.form = formData.kin
      }
    },
    store () {
      const formData = JSON.parse(localStorage.getItem('form'))
      formData.kin = this.form
      localStorage.setItem('form', JSON.stringify(formData))
    }

  }
}
</script>




