<template>
  <button :title="path" @click='toggle(path,type)' :class="styling"><i :class="icon"></i>{{name}}</button>
</template>
<script>
export default {
  props: {
    item: Object,
    title: {
      type: String,
      default: 'Table'
    },
    type: {
      type: String,
      default: 'link'
    },
    resource: {
      type: String,
      default: 'link'
    },
    icon: {
      type: String,
      default: ''
    },
    design: {
      type: String,
      default: 'info'
    },
    path: {
      type: String,
      default: ''
    },
    name: {
      type: String,
      default: ''
    }
  },
  mounted () {},
  computed: {
    styling () {
      return `has-tooltip btn btn-rounded btn-xs btn-outline-${this.design} `
    },
    buttonType () {
      return `${this.type}`
    },
    location () {
      return `${this.resource}/${this.path}/`
    },
    lineItem () {
      return this.item
    }
  },
  data: function () {
    return {
      isOpen: false
    }
  },
  methods: {
    toggle: function (action, type) {
      if (type === 'button') {
        this.$emit('listButtonEvent', this.item, this.path)
      } else {
        this.$router.push({
          name: action,
          params: {
            id: this.item.id
          }
        })
      }
    }
  }
}
</script>
<style>
.btn-xs{font-size: 10px}
.btn-round{border-radius: 50px}
</style>



