<template>
    <div class="row">
        <div class="col-12">
            <div class="card">
                <div class="card-body">
                    <h4 class="card-title">{{pageTitle}}
                  <small>Fields with <code>*</code> are required</small>
                </h4>
                    <form id="example-vertical-wizard" action="#">
                        <div role="application" class="wizard clearfix vertical" id="steps-uid-1">
                            <div class="steps clearfix">
                                <ul role="tablist">
                                  <li role="tab" v-for="(stepName, st) in steps" :key="st" :class="(step==st)?'current':'done'" :aria-selected="(step==1)">
                                      <a aria-controls="verticle-wizard-p-0">
                                          <span class="number">{{st+1}}.</span>  {{stepName}} 
                                      </a>
                                  </li>
                                </ul>
                            </div>
                            <div class="content clearfix">
                              <fieldset v-if="isCurrent('Registration')" role="tabpanel" :class="(isCurrent('Registration'))?'body current':'body'">
                                <h3>Registration</h3>
                                  <registration></registration>
                              </fieldset>
                              <fieldset v-if="isCurrent('Spouse Information')" role="tabpanel" :class="(isCurrent('Spouse Information'))?'body current':'body'">
                                <h3>Spouse Information</h3>
                                  <registration :spouse="true"></registration>
                              </fieldset>
                              <fieldset v-if="isCurrent('Child Information')" role="tabpanel" :class="(isCurrent('Child Information'))?'body current':'body'">
                                <h3>Child Information</h3>
                                  <child></child>
                              </fieldset>
                              <fieldset v-if="isCurrent('Next of Kin')" role="tabpanel" :class="(isCurrent('Next of Kin'))?'body current':'body'">
                                <h3>Next of Kin</h3>
                                  <kin></kin>
                              </fieldset>
                              <fieldset v-if="isCurrent('Payment Plan')" role="tabpanel" :class="(isCurrent('Payment Plan'))?'body current':'body'">
                                <h3>Payment Plan</h3>
                                <plan></plan>
                              </fieldset>
                            </div>
                            <div class="actions clearfix">
                                <ul role="menu" aria-label="Pagination">
                                    <a  v-if="(step != 0)" class="btn btn-default btn-sm" @click="previous()" >Previous</a>
                                    <a v-if="hasNext()" class="btn btn-primary btn-sm" @click="next()" >Next</a>
                                    <a v-if="isFinal()" class="btn btn-primary btn-sm" @click="finish()" >Finish</a>
                                </ul>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import Registration from './Registration'
import Child from './Child'
import Kin from './Kin'
import Plan from './Plan'
export default {
  data () {
    return {
      id: 0,
      form: {
        firstName: '',
        children: [],
        status: 0
      },
      title: '',
      edit: false,
      submitting: false,
      step: 0,
      steps: []
    }
  },
  components: {
    Registration,
    Child,
    Kin,
    Plan
  },
  computed: {
    pageTitle () {
      if (this.edit) return `Edit ${this.category} subscription form`
      return `Fill your ${this.category} subscription form`
    }
  },
  created () {
    this.category = this.$categories.categoryDisplayName(
      this.$route.params.category
    )
    this.generateSteps()
    this.init()
  },
  methods: {
    finish () {
      const formData = JSON.parse(localStorage.getItem('form'))
      if (!formData.plan) {
        this.$toastr('error', 'Select plan first')
        return
      }
      this.$router.push({
        name: 'review'
      })
    },
    next () {
      if (this.validated(this.step)) {
        this.step = this.step + 1
      } else {
        this.$toastr('info', 'Fields with * are required')
      }
    },
    generateSteps () {
      const steps = ['Registration']
      switch (this.$route.params.category) {
        case 'couple':
          steps.push('Spouse Information')
          break
        case 'single-parent':
          steps.push('Child Information')
          break
        case 'family':
          steps.push('Spouse Information')
          steps.push('Child Information')
          break
        default:
          break
      }
      steps.push('Next of Kin')
      steps.push('Payment Plan')
      this.steps = steps
    },
    previous () {
      this.step = this.step - 1
    },
    hasNext () {
      if (this.step < this.steps.length - 1) {
        return true
      }
      return false
    },
    isCurrent (field) {
      if (this.step === this.steps.indexOf(field)) {
        return true
      }
      return false
    },
    isFinal () {
      if (this.step === this.steps.length - 1) {
        return true
      }
      return false
    },
    init () {
      const formData = JSON.parse(localStorage.getItem('form'))
      let user = JSON.parse(localStorage.getItem('user'))
      if (formData === null || formData.user !== user.uid) {
        this.form.firstName = user.name.split(' ')[0]
        this.form.lastName = user.name.split(' ')[1]
        this.form.user = user.uid
        this.form.category = this.$route.params.category
      } else {
        if (formData.category !== this.$route.params.category) {
          this.$swal({
            text: 'Would you like to change your plan from ' +
            this.$categories.categoryDisplayName(formData.category) + ' to ' + this.$categories.categoryDisplayName(
              this.$route.params.category) + '?',
            type: 'info',
            title: 'You have a partially filled form',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            cancelButtonText: 'No',
            confirmButtonText: 'Yes'
          }).then((result) => {
            if (result.value) {
              formData.category = this.$route.params.category
              switch (formData.category) {
                case 'single':
                  formData.children = null
                  formData.spouse = null
                  break
                case 'senior':
                  formData.children = null
                  formData.spouse = null
                  break
                case 'couple':
                  formData.children = null
                  formData.spouse = null
                  break
                case 'single-parent':
                  formData.spouse = null
                  break
                default:
                  break
              }
              localStorage.setItem('form', JSON.stringify(formData))
              this.$router.push({
                name: 'fill-form',
                params: { category: this.$route.params.category }
              })
            } else {
              this.$router.push({
                name: 'fill-form',
                params: { category: formData.category }
              })
            }
            location.reload()
          })
        }
        this.form = formData
      }
      localStorage.setItem('form', JSON.stringify(this.form))
    },
    validated (step) {
      const formData = JSON.parse(localStorage.getItem('form'))
      const stepName = this.steps[step]
      switch (stepName) {
        case 'Registration':
          if (formData === null || typeof formData === 'undefined') {
            return false
          }
          if (formData.firstName === null || formData.firstName === '' || typeof formData.firstName === 'undefined') {
            return false
          }
          if (formData.lastName === null || formData.lastName === '' || typeof formData.lastName === 'undefined') {
            return false
          }
          if (formData.dateOfBirth === null || formData.dateOfBirth === '' || typeof formData.dateOfBirth === 'undefined') {
            return false
          }
          if (formData.gender === null || formData.gender === '' || typeof formData.gender === 'undefined') {
            return false
          }
          if (formData.phoneNumber === null || formData.phoneNumber === '' || typeof formData.phoneNumber === 'undefined') {
            return false
          }
          if (formData.country === null || formData.country === '' || typeof formData.country === 'undefined') {
            return false
          }
          if (formData.physicalAddress === null || formData.physicalAddress === '' || typeof formData.physicalAddress === 'undefined') {
            return false
          }
          if (formData.state === null || formData.state === '' || typeof formData.state === 'undefined') {
            return false
          }
          if (formData.city === null || formData.city === '' || typeof formData.city === 'undefined') {
            return false
          }
          if (formData.homeCountry === null || formData.homeCountry === '' || typeof formData.homeCountry === 'undefined') {
            return false
          }
          break
        case 'Spouse Information':
          if (formData.spouse === null || typeof formData.spouse === 'undefined') {
            return false
          }
          if (formData.spouse.firstName === null || formData.spouse.firstName === '' || typeof formData.spouse.firstName === 'undefined') {
            return false
          }
          if (formData.spouse.lastName === null || formData.spouse.lastName === '' || typeof formData.spouse.lastName === 'undefined') {
            return false
          }
          if (formData.spouse.dateOfBirth === null || formData.spouse.dateOfBirth === '' || typeof formData.spouse.dateOfBirth === 'undefined') {
            return false
          }
          if (formData.spouse.gender === null || formData.spouse.gender === '' || typeof formData.spouse.gender === 'undefined') {
            return false
          }
          if (formData.spouse.phoneNumber === null || formData.spouse.phoneNumber === '' || typeof formData.spouse.phoneNumber === 'undefined') {
            return false
          }
          if (formData.spouse.country === null || formData.spouse.country === '' || typeof formData.spouse.country === 'undefined') {
            return false
          }
          if (formData.spouse.physicalAddress === null || formData.spouse.physicalAddress === '' || typeof formData.spouse.physicalAddress === 'undefined') {
            return false
          }
          if (formData.spouse.state === null || formData.spouse.state === '' || typeof formData.spouse.state === 'undefined') {
            return false
          }
          if (formData.spouse.city === null || formData.spouse.city === '' || typeof formData.spouse.city === 'undefined') {
            return false
          }
          if (formData.spouse.homeCountry === null || formData.spouse.homeCountry === '' || typeof formData.spouse.homeCountry === 'undefined') {
            return false
          }
          break
        case 'Next of Kin':
          if (formData.kin === null || typeof formData.kin === 'undefined') {
            return false
          }
          if (formData.kin.fullname === null || formData.kin.fullname === '' || typeof formData.kin.fullname === 'undefined') {
            return false
          }
          if (formData.kin.phoneNumber === null || formData.kin.phoneNumber === '' || typeof formData.kin.phoneNumber === 'undefined') {
            return false
          }
          if (formData.kin.email === null || formData.kin.email === '' || typeof formData.kin.email === 'undefined') {
            return false
          }
          break
        default:
          break
      }
      return true
    }
  }
}
</script>


