<template>
  <div class='row'>
    <div class='col-md-12 grid-margin stretch-card'>
      <div class='card'>
        <data-table 
        :canAdd='false'
        v-on:buttonEvent='actionHandler' 
        v-on:event='pages' :actions='actions' 
        :loading='loading' 
        :pagination='pagination' 
        :title='title' 
        :sub-title='subTitle' 
        :data='data' 
        :columns='columns'>
        </data-table>
      </div>
    </div>
  </div>
</template>
<script>
import { database } from '../../store/settings.module'
const subscriptionsRef = database.ref('subscriptions')
export default {
  components: {
  },
  data () {
    return {
      pagination: {},
      loading: true,
      title: 'Subscriptions',
      subTitle: 'All the subscriptions ',
      columns: [
        {
          name: 'Name',
          type: ''
        },
        {
          name: 'Category',
          type: 'code'
        },
        {
          name: 'Gender',
          type: ''
        },
        {
          name: 'Children',
          type: 'badge'
        }
      ],
      actions: [
        {
          name: '',
          icon: 'icon-eye',
          type: 'link',
          path: 'subscription-detail',
          design: 'success'
        }
      ],
      data: []
    }
  },
  created () {
    this.pages(10, 0)
  },
  methods: {
    pages: function (itemsPerPage, pageNumber) {
      this.loading = true
      subscriptionsRef.once('value', subscriptions => {
        subscriptions.forEach(subscription => {
          this.data.push({
            id: subscription.ref.key,
            reference: subscription.ref.key,
            name: subscription.child('firstName').val() + ' ' + subscription.child('lastName').val(),
            category: subscription.child('category').val(),
            gender: subscription.child('gender').val(),
            children: (subscription.child('children').val() == null) ? 0 : subscription.child('children').val().length
          })
        })
        this.loading = false
      })
    },
    actionHandler: function (item, action) {
      switch (action) {
        case 'delete':
          this.$http
            .post('/' + action, item)
            .then(result => {
              this.$toastr('success', 'Success')
              this.pages(10, this.pagination.current)
            })
            .catch(error => {
              this.$toastr('error', error.message)
            })
          break

        default:
          break
      }
    }
  }
}
</script>
<style>

</style>



