const MethodsPlugin = {}
MethodsPlugin.install = function (Vue, options) {
  Vue.prototype.$baseStore = function (key, data) {
    localStorage.setItem(key, null)
    localStorage.setItem(key, btoa(JSON.stringify(data)))
  }

  Vue.prototype.$commaSeperate = function (num) {
    var nStr = parseFloat(Math.round(num * 100) / 100).toFixed(2)
    nStr += ''
    var x = nStr.split('.')
    var x1 = x[0]
    var x2 = x.length > 1 ? '.' + x[1] : ''
    var rgx = /(\d+)(\d{3})/
    while (rgx.test(x1)) {
      x1 = x1.replace(rgx, '$1' + ',' + '$2')
    }
    return x1 + x2
  }
  Vue.prototype.$concatenateHeader = function (column) {
    var result = ''
    var columnArray = column.split(' ')
    if (columnArray.length > 1) {
      result = columnArray[0].concat(columnArray[1])
    } else result = column
    return result.toLowerCase()
  }
  Vue.prototype.$baseRead = function (key) {
    var res = localStorage.getItem(key)
    var data = res.length < 5 ? null : JSON.parse(atob(res))
    return data
  }
  Vue.prototype.$capitalizeFirstLetter = function (string) {
    return string.charAt(0).toUpperCase() + string.slice(1)
  }
  Vue.prototype.$baseLinkClass = function (to) {
    if (to) {
      var styling = 'nav-link'
      if (this.$route.path.includes(to)) styling = styling + ' active'
      return styling
    }
  }
  Vue.prototype.$makePagination = function (data) {
    return {
      current: data.current_page,
      last: data.last_page,
      total: data.total,
      items: data.per_page,
      nextUrl: data.next_page_url,
      prevUrl: data.prev_page_url,
      visible: true
    }
  }
  Vue.prototype.$splitId = function (id) {
    return id.substring(0, id.indexOf('-'))
  }
  Vue.prototype.$returnAge = function (dateOfBirth) {
    let dateOfBirthArray = dateOfBirth.split('-')
    var today = new Date()
    let age = today.getFullYear() - dateOfBirthArray[0]
    return age
  }
  Vue.prototype.$formatColumn = function (format, value) {
    var display = ''
    switch (format) {
      case 'currency':
        display = '<span class="pull-right">' + this.$commaSeperate(value) + '</span>'
        break
      case 'centered':
        display = '<span class="text-center">' + value + '</span>'
        break
      case 'numeric':
        display = '<span class="pull-right">' + value + '</span>'
        break
      case 'badge':
        display = '<label class="badge badge-primary">' + value + '</label>'
        break
      case 'code':
        display = '<code>' + value + '</code>'
        break
      case 'story':
        display = '<small>' + value + '</small>'
        break
      case 'title':
        display = '<span class="caps">' + value + '</span>'
        break
      default:
        display = value
        break
    }
    return display
  }
}
export default MethodsPlugin



