<template>
    <div>
        <div class='row'>
            <div class='col-md-12'>
                <div class='card'>
                    <div class="card-body">
                    <span class="badge badge-success pull-right">{{pagination.total}}</span>
                        <slot name="header">
                            <h4 class="title">{{title}}</h4>
                            <p class="category">{{subTitle}}
                            </p>
                        </slot>
                    </div>
         
                    <div class="container">
                      <div class="row">
                        <div class='col-md-12'>
<br>
                          <div class="timeline-centered">
                              <article v-for="(item,index) in data" :key="index" class="timeline-entry left-aligned">
                                  <div class="timeline-entry-inner">
                                      <time class="timeline-time" datetime="2014-01-10T03:45"><span class="caps">{{item.type}}</span> <span>{{item.date}}</span></time>
                                      <div class="timeline-icon bg-primary">
                                          <i :class="icon(item.type)"></i>
                                      </div>
                                      <div class="timeline-label">
                                          <h2><a>{{item.name}}</a> <span>created by</span> {{item.user}}</h2>
                                          <blockquote>{{item.description}}</blockquote>
                                          <span class="badge caps">{{item.project}}</span>
                                          <hr>
                                          <div v-if="item.type!='progress'">
                                            <router-link v-if="item.type!='receipt'" class="btn btn-primary" :to="{ name: 'document-detail', params: { id: item.id } }"><i class="icon-eye"></i> View </router-link>
                                            <router-link v-if="item.type=='receipt'" class="btn btn-primary" :to="{ name: 'print-payment', params: { id: item.id } }"><i class="icon-eye"></i> View </router-link>
                                          </div>
                                
                                      </div>
                                  </div>
                              </article>
                              <article class="timeline-entry begin">
                                  <div class="timeline-entry-inner">
                                      <div class="timeline-icon" style="-webkit-transform: rotate(-90deg); -moz-transform: rotate(-90deg);">
                                          <i class="icon-flight"></i>
                                      </div>
                                  </div>
                              </article>
                          </div>
                      </div>
                      </div>
                  </div>

                </div>
            </div>
        </div>
    </div>
</template>
<script>
const resource = 'documents'
export default {
  components: {},
  data () {
    return {
      id: 0,
      pagination: {},
      resource: resource,
      loading: true,
      title: ' Timeline',
      subTitle: '',
      data: [],
      source: 'subscriptions'
    }
  },
  created () {
    this.id = this.$route.params.id
    if (this.$route.name === 'timeline-view') {
      this.source = 'projects'
    }
    this.fetch(this.source)
  },
  computed: {
    pageTitle () {
      if (this.client) return `Viewing timeline for:  ${this.client.name}`

      this.title = 'Viewing timeline'
    }
  },
  methods: {
    fetch () {
      this.$http
        .get(this.source + '/' + this.id)
        .then(result => {
          this.client = result.data.data
          if (this.source === 'projects') {
            this.client.name = result.data.data.title
          }
          this.data = []
          this.subTitle = 'Timeline for : ' + this.client.name
          var documents = this.client.documents
          var payments = this.client.payments
          var progressItems = this.client.progress
          progressItems.forEach(element => {
            var item = {}
            item.id = element.id
            item.name = 'Progress Update'
            item.description = element.description + ' - status: ' + element.stage
            item.date = element.created_at
            item.user = element.user.name
            item.project = element.project.title
            item.type = 'progress'
            this.data.push(item)
          })
          payments.forEach(element => {
            var item = {}
            item.id = element.id
            item.name = element.narration
            item.description = element.reference
            item.date = element.created_at
            item.user = element.user.name
            item.project = element.project.title
            item.type = 'receipt'
            this.data.push(item)
          })
          documents.forEach(element => {
            var item = {}
            item.id = element.id
            item.name = element.name
            item.description = element.description
            item.date = element.created_at
            item.user = element.user.name
            item.project = element.project.title
            item.type = element.type
            this.data.push(item)
          })
          this.data = this.data.sort(this.dynamicSort('-date'))
          this.pagination = this.$makePagination({ current: 1, total: this.data.length })
        })
        .catch(error => {
          this.$toastr('error', error.message)
        })
    },
    dynamicSort (property) {
      var sortOrder = 1
      if (property[0] === '-') {
        sortOrder = -1
        property = property.substr(1)
      }
      return function (a, b) {
        var result = (a[property] < b[property]) ? -1 : (a[property] > b[property]) ? 1 : 0
        return result * sortOrder
      }
    },
    icon (type) {
      var icon = 'icon-info-circle'
      switch (type) {
        case 'quotation':
          icon = 'icon-comments'
          break
        case 'delivery-note':
          icon = 'icon-truck'
          break
        case 'goods-received-note':
          icon = 'icon-comments'
          break
        case 'contract':
          icon = 'icon-edit'
          break
        case 'receipt':
          icon = 'icon-check-circle'
          break
        case 'invoice':
          icon = 'icon-hourglass'
          break
        default:
          break
      }
      return icon
    },
    actionHandler: function (item, action) {
      switch (action) {
        case 'delete':
          this.$http
            .post(this.resource + '/' + action, item)
            .then(result => {
              this.$toastr('success', 'Success')
              this.fetch()
            })
            .catch(error => {
              this.$toastr('error', error.message)
            })
          break

        default:
          break
      }
    }
  }
}
</script>
<style>
.timeline-centered {
    position: relative;
    margin-bottom: 30px;
}

.timeline-centered:before,
.timeline-centered:after {
    content: " ";
    display: table;
}

.timeline-centered:after {
    clear: both;
}

.timeline-centered:before,
.timeline-centered:after {
    content: " ";
    display: table;
}

.timeline-centered:after {
    clear: both;
}

.timeline-centered:before {
    content: '';
    position: absolute;
    display: block;
    width: 4px;
    background: #f5f5f6;
    left: 50%;
    top: 20px;
    bottom: 20px;
    margin-left: -4px;
}

.timeline-centered .timeline-entry {
    position: relative;
    width: 50%;
    float: right;
    margin-bottom: 70px;
    clear: both;
}

.timeline-centered .timeline-entry:before,
.timeline-centered .timeline-entry:after {
    content: " ";
    display: table;
}

.timeline-centered .timeline-entry:after {
    clear: both;
}

.timeline-centered .timeline-entry:before,
.timeline-centered .timeline-entry:after {
    content: " ";
    display: table;
}

.timeline-centered .timeline-entry:after {
    clear: both;
}

.timeline-centered .timeline-entry.begin {
    margin-bottom: 0;
}

.timeline-centered .timeline-entry.left-aligned {
    float: left;
}
 
.timeline-centered .timeline-entry.left-aligned .timeline-entry-inner {
    margin-left: 0;
    margin-right: -18px;  
}

.timeline-centered .timeline-entry.left-aligned .timeline-entry-inner .timeline-time {
    left: auto;
    right: -150px;
    text-align: left;
}

.timeline-centered .timeline-entry.left-aligned .timeline-entry-inner .timeline-icon {
    float: right;
}

.timeline-centered .timeline-entry.left-aligned .timeline-entry-inner .timeline-label {
    margin-left: 0;
    margin-right: 70px;
}

.timeline-centered .timeline-entry.left-aligned .timeline-entry-inner .timeline-label:after {
    left: auto;
    right: 0;
    margin-left: 0;
    margin-right: -9px;
    -moz-transform: rotate(180deg);
    -o-transform: rotate(180deg);
    -webkit-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    transform: rotate(180deg);
}

.timeline-centered .timeline-entry .timeline-entry-inner {
    position: relative;
    margin-left: -22px;
}

.timeline-centered .timeline-entry .timeline-entry-inner:before,
.timeline-centered .timeline-entry .timeline-entry-inner:after {
    content: " ";
    display: table;
}

.timeline-centered .timeline-entry .timeline-entry-inner:after {
    clear: both;
}

.timeline-centered .timeline-entry .timeline-entry-inner:before,
.timeline-centered .timeline-entry .timeline-entry-inner:after {
    content: " ";
    display: table;
}

.timeline-centered .timeline-entry .timeline-entry-inner:after {
    clear: both;
}

.timeline-centered .timeline-entry .timeline-entry-inner .timeline-time {
    position: absolute;
    left: -100px;
    text-align: right;
    padding: 10px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

.timeline-centered .timeline-entry .timeline-entry-inner .timeline-time>span {
    display: block;
}

.timeline-centered .timeline-entry .timeline-entry-inner .timeline-time>span:first-child {
    font-size: 15px;
    font-weight: bold;
}

.timeline-centered .timeline-entry .timeline-entry-inner .timeline-time>span:last-child {
    font-size: 12px;
}

.timeline-centered .timeline-entry .timeline-entry-inner .timeline-icon {
    background: #fff;
    color: #737881;
    display: block;
    width: 40px;
    height: 40px;
    -webkit-background-clip: padding-box;
    -moz-background-clip: padding;
    background-clip: padding-box;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    border-radius: 20px;
    text-align: center;
    -moz-box-shadow: 0 0 0 5px #f5f5f6;
    -webkit-box-shadow: 0 0 0 5px #f5f5f6;
    box-shadow: 0 0 0 5px #f5f5f6;
    line-height: 40px;
    font-size: 15px;
    float: left;
}

.timeline-centered .timeline-entry .timeline-entry-inner .timeline-icon.bg-primary {
    background-color: #303641;
    color: #fff;
}

.timeline-centered .timeline-entry .timeline-entry-inner .timeline-icon.bg-secondary {
    background-color: #ee4749;
    color: #fff;
}

.timeline-centered .timeline-entry .timeline-entry-inner .timeline-icon.bg-success {
    background-color: #00a651;
    color: #fff;
}

.timeline-centered .timeline-entry .timeline-entry-inner .timeline-icon.bg-info {
    background-color: #21a9e1;
    color: #fff;
}

.timeline-centered .timeline-entry .timeline-entry-inner .timeline-icon.bg-warning {
    background-color: #fad839;
    color: #fff;
}

.timeline-centered .timeline-entry .timeline-entry-inner .timeline-icon.bg-danger {
    background-color: #cc2424;
    color: #fff;
}


        .timeline > li > .timeline-panel {
            width: 46%;
            float: left;
            border: 1px solid #d4d4d4;
            border-radius: 2px;
            padding: 20px;
            position: relative;
            -webkit-box-shadow: 0 1px 6px rgba(0, 0, 0, 0.175);
            box-shadow: 0 1px 6px rgba(0, 0, 0, 0.175);
        }

.timeline-centered .timeline-entry .timeline-entry-inner .timeline-label {
    position: relative;
/*     background: #f5f5f6; */
    padding: 1.7em;
    margin-left: 70px;
    -webkit-background-clip: padding-box;
    -moz-background-clip: padding;
    background-clip: padding-box;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 2px;

    border: 1px solid #d4d4d4;
    -webkit-box-shadow: 0 1px 6px rgba(0, 0, 0, 0.175);
    box-shadow: 0 1px 6px rgba(0, 0, 0, 0.175); 
/* 
    float: left;
    width: 90%;
    padding: 20px;
    */
}

.timeline-centered .timeline-entry .timeline-entry-inner .timeline-label:after {
    content: '';
    display: block;
    position: absolute;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 9px 9px 9px 0;
    border-color: transparent #f5f5f6 transparent transparent;
    left: 0;
    top: 10px;
    margin-left: -9px;

    
}

.timeline-centered .timeline-entry .timeline-entry-inner .timeline-label h2,
.timeline-centered .timeline-entry .timeline-entry-inner .timeline-label p {
    color: #737881;
    font-family: "Noto Sans", sans-serif;
    font-size: 12px;
    margin: 0;
    line-height: 1.428571429;
}

.timeline-centered .timeline-entry .timeline-entry-inner .timeline-label p+p {
    margin-top: 15px;
}

.timeline-centered .timeline-entry .timeline-entry-inner .timeline-label h2 {
    font-size: 16px;
    margin-bottom: 10px;
}

.timeline-centered .timeline-entry .timeline-entry-inner .timeline-label h2 a {
    color: #303641;
}

.timeline-centered .timeline-entry .timeline-entry-inner .timeline-label h2 span {
    -webkit-opacity: .6;
    -moz-opacity: .6;
    opacity: .6;
    -ms-filter: alpha(opacity=60);
    filter: alpha(opacity=60);
}
</style>


