<template>
    <div class='row'>
			<div class="col-lg-12 col-md-12">

      	<div class='tabbable-panel card'>
				<div class='tabbable-line'>
					<ul class='nav nav-tabs '>
						<li @click="routeTo('branding')" v-bind:class="{'active':(routerName.includes('branding'))}" >
							<a data-toggle='tab'>
								Branding
							</a>
						</li>
		 				<li @click="routeTo('profile')" v-bind:class="{'active':(routerName.includes('profile'))}">
							<a data-toggle='tab'>
								Profile 
							</a>
						</li> 
					</ul>
					<div class='tab-content'>
						<div class='tab-content'>
								<router-view></router-view>
						</div>
					</div>
				</div>
			</div>
			</div>

      <div class='col-lg-8 col-md-7'>
        
      </div>
    </div>
</template>
<script>
export default {
  computed: {
    routerName () {
      const { name } = this.$route
      return name
    }
  },
  methods: {
    routeTo (to) {
      this.$router.push({ name: to })
    }
  }
}
</script>
<style></style>


