<template>
  <div class="card mb-3">
    <div class="card-body">
      <h4 class='title'>{{pageTitle}}</h4>
    </div>
 

<!-- Tab panes -->

    <div class="card-body">
      <div class="row">
        <div class="col-md-12">
          <div class="feed-toggle">
            <ul class="nav nav-pills outline-active">
              <li @click="activeTab=1" class="nav-item">
                <a class="nav-link" v-bind:class="{'active':(activeTab === 1)}">subscriptions 
                    <span v-if="activeTab === 1">
                      {{subscriptions}}
                    </span>
                </a>
              </li>
              <li @click="activeTab=2" class="nav-item">
                <a class="nav-link" v-bind:class="{'active':(activeTab === 2)}">documents 
                   <span v-if="activeTab === 2">
                      {{documents}}
                    </span>
                </a>
              </li>
              <li @click="activeTab=3" class="nav-item">
                <a class="nav-link" v-bind:class="{'active':(activeTab === 3)}">Payments 
                   <span v-if="activeTab === 3">
                      {{payments}}
                    </span>
                </a>
              </li>
            </ul>
          </div>
          <div class="tab-content">
            <div class="tab-pane active" id="home" style="color:#000">
              <div  v-for="(item,index) in data" :key="index" class="items col-xs-12 col-sm-12 col-md-12 col-lg-12 clearfix">
                <div class=" info-block block-info clearfix">
                  <h4>{{item.name}}</h4>
                  <h5>{{item.description}}</h5>
                  <small>Status: {{item.status}}</small><br>
                  <small v-if="item.state">State: {{item.state}}</small>
                  <router-link :title="item.path" :to="{ name: item.location, params: { id: item.id }}" class='pull-right btn btn-xs btn-primary'>
                    View Details </router-link>
                    </div>
              </div>
          </div>
        </div>
      </div>
    </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      searchTerm: 0,
      activeTab: 0,
      subscriptions: 0,
      documents: 0,
      payments: 0,
      searchResults: {},
      data: {}
    }
  },
  computed: {
    pageTitle () {
      return `Search Results for: ${this.$route.params.searchTerm}`
    }
  },
  created () {
    if (this.$route.params.searchTerm) {
      this.searchTerm = this.$route.params.searchTerm
      this.fetch()
      this.edit = true
    }
  },
  watch: {
    activeTab: function (val) {
      this.setData(val)
    }
  },
  methods: {
    fetch () {
      var search = {
        searchTerm: this.searchTerm
      }
      this.$http
        .post('search', search)
        .then(result => {
          this.searchResults = {}
          this.searchResults = result.data.data
        })
        .catch(error => { this.$toastr('error', error.message) })
    },
    setData (pageNumber) {
      switch (pageNumber) {
        case 1:
          this.data = []
          var info = this.searchResults.subscriptions.items
          this.subscriptions = this.searchResults.subscriptions.count
          info.forEach(element => {
            var item = {
              id: element.id,
              state: element.occupied === 1 ? 'Occupied' : 'Available',
              status: element.status === 1 ? 'Deleted' : 'Active',
              name: element.name,
              description: element.floor,
              location: 'view-timeline'
            }
            this.data.push(item)
          })
          break
        case 2:
          this.data = []
          this.documents = this.searchResults.documents.count
          this.searchResults.documents.items.forEach(element => {
            var item = {
              id: element.id,
              description: element.contactName + ' - ' + element.phoneNumber,
              name: element.businessName,
              status: element.status === 1 ? 'Moved' : 'Active',
              location: 'document-detail'
            }
            this.data.push(item)
          })
          break
        case 3:
          this.data = []
          this.payments = this.searchResults.payments.count
          this.searchResults.payments.items.forEach(element => {
            var item = {
              id: element.id,
              location: 'print-payment',
              name: element.reference,
              description: element.method,
              status: element.balance > 0 ? 'Not paid in full' : 'Fully Paid'
            }
            this.data.push(item)
          })
          break
        default:
          break
      }
    }
  }
}
</script>
<style>
.fa-lg{font-size: 2em}
.info-block
{
    border-right:5px solid #E6E6E6;margin-bottom:25px;
  border:#20819e 1px solid;
    padding:10px
}
.info-block:hover{
  border-left:5px solid #E6E6E6;
}
.info-block .square-box
{
    width:50px;
    min-height:50px;
    margin-right:22px;text-align:center!important;background-color:#676767;padding:20px 0
}
.info-block.block-info
{
    border-color:#20819e
}
.info-block.block-info .square-box
{
    background-color:#20819e;color:#FFF
}
</style>



