<template>
    <div>
      <div class='row'>
            <div class="col-lg-12 grid-margin stretch-card">
                <div class="card card-weather">
                    <div class="card-body">
                        <div class="weather-date-location">
                            <h3>Welcome back, {{currentUser.name}}</h3>
                            <p class="text-gray">
                                <span class="weather-date">Here are your stats </span>
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="row">
            <div class="col-md-12 col-lg-4">
                <div class="card">
                    <div class="card-body text-center">
                        <i class="feather icon-mail text-c-blue d-block f-40"></i>
                        <h4 class="m-t-20"><span class="text-c-blue">{{subscriptions}}</span> Subscribers</h4>
                        <p class="m-b-20"> main list is growing</p>
                        <button class="btn btn-primary btn-sm btn-round">Manage List</button>
                    </div>
                </div>
            </div>
            <div class="col-md-6 col-lg-4">
                <div class="card">
                    <div class="card-body text-center">
                        <i class="feather icon-file-text text-c-green d-block f-40"></i>
                        <h4 class="m-t-20"><span class="text-c-green">{{users}}</span> Users</h4>
                        <p class="m-b-20"> users</p>
                        <button class="btn btn-success btn-sm btn-round">Check them out</button>
                    </div>
                </div>
            </div>
            <div class="col-md-6 col-lg-4">
                <div class="card">
                    <div class="card-body text-center">
                        <i class="feather icon-file-text text-c-red d-block f-40"></i>
                        <h4 class="m-t-20"><span class="text-c-red">{{payments}}</span> Payments</h4>
                        <p class="m-b-20"> payments are growing</p>
                        <button class="btn btn-danger btn-sm btn-round">Have a look</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
  components: {
  },
  data () {
    return {
      users: 0,
      subscriptions: 0,
      payments: 0
    }
  },
  computed: {
    currentUser () {
      return JSON.parse(localStorage.getItem('user'))
    }
  },
  created () {
    // this.subscriptionsOverview()
    // this.documentsOverview()
    // this.usersOverview()
    // this.jobsOverview()
    // this.paymentsOverview()
    this.plan = JSON.parse(localStorage.getItem('user')).plan
    this.$categories.categoryItems.forEach(element => {
      element.count = 0
      this.categories.push(element)
    })
  },
  mounted () {
  },
  methods: {
    paymentsOverview () {
      this.$http
        .get('payments/pages/1000/from/Jan-2016/to/Dec-2020?page=1')
        .then(result => {
          this.statsCards[1].value = result.data.total
          var sum = 0
          result.data.data.forEach(element => {
            sum = sum + parseInt(element.amount)
          })
          this.payments = sum
        })
        .catch(error => { this.$toastr('error', error.message) })
    },
    usersOverview () {
      this.$http
        .get('users/pages/1?page=1')
        .then(result => {
          this.statsCards[3].value = result.data.total
        })
        .catch(error => { this.$toastr('error', error.message) })
    },
    selectCategory (category) {
      const categoryDetails = this.$categories.categoryDetails(category)
      this.$swal({
        title: 'You have selected the ' + categoryDetails.display + ' subscription',
        text: "You won't be able to revert this!",
        type: 'info',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes, continue!'
      }).then((result) => {
        if (result.value) {
          this.$router.push({
            name: 'fill-form',
            params: { category: category }
          })
        }
      })
    }
  }
}
</script>
<style>
.card .numbers {
    font-size: 2em;
    text-align: right;
}
p {
    font-size: 16px;
    line-height: 1.4em;
}

.card .icon-big {
    font-size: 2.5em;
    min-height: 64px;
}
.stats{font-size: 1em; font-weight: 100;}
</style>



