<template>
    <div>
        <span class="badge badge-success pull-right">{{pagination.total}}</span>
        <div class="card-body">
            <slot name="header">
                <h4 class="card-title">{{title}}</h4>
                <p class="card-description">{{subTitle}}</p>
            </slot>
            <hr>
              <div class="row text-small">
                  <div class="col-sm-12 col-md-8">
                      <form class="form-inline my-4 my-lg-0 mr-lg-4">
                        <div class="input-group"><input type="text" placeholder="Search for..." class="form-control"> <span class="input-group-append"><button type="button" class="btn btn-primary"><i class="icon-search"></i></button></span></div></form>
                  </div>
                  <div class="col-sm-12 col-md-4 m-b-mini">
                      <router-link v-if="canAdd"
                      :to="{path: resource + '/add/'}" 
                      class="pull-right btn btn-sm btn-outline-primary btn-primary">Add
                    </router-link>
                  </div>
              </div>
        </div>
    
        <div class="card-body table-responsive ">
            <table class="table table-condensed table-bordered">
                <thead>
                  <tr>
                    <th v-for="(column,index) in columns" :key="index+'top'">{{column.name}}</th>
                    <th v-if="actions.length" style="text-align:center" :colspan="columnSpan">Actions</th>
                  </tr>
                </thead>
                <tbody v-if="loading">
                  <tr v-for="(ipp,ip) in itemsPerPage" :key="ip+'placeholders'">
                      <td v-for="(cols,cl) in columns.length + actions.length" :key="cl+'backgrounds'">
                          <div class="content-placeholder-text"><span class="content-placeholder-background"></span></div>
                      </td>
                  </tr>
              </tbody>
                <tbody v-if="!loading">
                    <tr v-for="(item,index) in data" :key="index+'row'">
                      <td v-for="(column,index) in columns" :key="index+'column'" v-if="hasValue(item, column)" v-html="itemValue(item, column)"></td>                        
                            <td v-if="actions.length" v-for="(act,a) in actions" :key="a+'action-column'">
                            <list-button 
                            v-on:listButtonEvent="relay"
                            :resource="resource" 
                            :icon="act.icon" 
                            :design="act.design" 
                            :name="act.name" 
                            :type="act.type"
                            :item="item"
                            :path="act.path"
                            ></list-button>
                        </td>
                    </tr>
                </tbody>
                <tfoot>
                  <tr>
                    <th v-for="(column,index) in columns" :key="index+'bottom'">{{column.name}}</th>
                    <th v-if="actions.length" style="text-align:center" :colspan="columnSpan">Actions</th>
                  </tr>
                </tfoot>
            </table>
        </div>
        <div v-if="pagination.visible" class="card-body">
            <paginate 
            :page-class="'paginate_button page-item'" 
            :page-link-class="'page-link'" 
            :prev-link-class="'page-link'" 
            :next-link-class="'page-link'" 
            :page-count="typeof pagination.last === 'undefined' ? 0 : pagination.last" 
            :click-handler="goToPage" 
            :prev-text="'Previous'" 
            :next-text="'Next'" 
            :container-class="'pagination pull-right'">
            </paginate>
            <div class="pull-left">
              <select v-model="itemsPerPage" v-on:change="changeItems" class="form-control">
                  <option selected :value="5">5</option>
                  <option :value="10">10</option>
                  <option :value="50">50</option>
                  <option :value="100">100</option>
              </select>
              <label >Page Size:{{itemsPerPage}}</label>
            </div>
        </div>
    </div>
</template>
<script>
import ListButton from '@/components/UIComponents/Inputs/ListButton.vue'
export default {
  components: {
    ListButton
  },
  props: {
    columns: Array,
    actions: Array,
    itemsPerPage: {
      type: Number,
      default: 10
    },
    totalItems: {
      type: Number,
      default: 0
    },
    searchText: '',
    loading: Boolean,
    pagination: Object,
    data: Array,
    type: {
      type: String, // striped | hover
      default: 'hover'
    },
    canAdd: {
      type: Boolean, // striped | hover
      default: true
    },
    title: {
      type: String,
      default: 'Table'
    },
    resource: {
      type: String,
      default: ''
    },
    subTitle: {
      type: String,
      default: ''
    }
  },
  computed: {
    columnSpan () {
      if (this.actions) return this.actions.length
      return 1
    }
  },
  created () {},
  methods: {
    hasValue (item, column) {
      return item[this.$concatenateHeader(column.name)] !== 'undefined'
    },
    itemValue (item, column) {
      var value = item[this.$concatenateHeader(column.name)]
      return this.$formatColumn(column.type, value)
    },
    changeItems () {
      this.goToPage(this.pagination.current)
    },
    goToPage (pageNum) {
      this.$emit('event', this.itemsPerPage, pageNum)
    },
    relay (item, action) {
      this.$emit('buttonEvent', item, action)
    }
  }
}
</script>
<style>
.no-radius {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
th {
    line-height: 3em;
}
</style>


