<template>
 <div class="row profile-page">
            <div class="col-12">
              <div class="card">
                <div class="card-body">
                  <div class="profile-header text-white">
                    <div class="d-md-flex justify-content-around">
                      <div class="profile-info d-flex align-items-center">
                        <div class="wrapper pl-4">
                          <p class="profile-user-name">{{form.firstName}} {{form.lastName}}</p>
                        <p class="profile-user-designation">{{pageTitle}}</p>

                        </div>
                      </div>
                      <div class="details">
                        <div class="detail-col">
                            <a v-if="filling" @click="back()" title="Edit" 
                             class="btn btn-primary pull-left"><span class="icon-arrow-left"></span> Back</a>
                            <br>
                            <br>
<!--                             <submit-button v-if="filling" :loading='submitting' v-on:submit="payNow()" :title="'Pay $ ' + form.plan.value + ' Now'" 
                            icon="icon-check-circle" class="btn btn-success pull-right"></submit-button>-->
                            <a  v-if="filling" @click="subscribeNow()" class="btn btn-success pull-right">Subscribe to paying $ {{form.plan.value}} {{form.plan.frequency}} </a>
                           
                        </div>
                      </div>
                    </div>
                  </div>
                  <div class="profile-body">
                    <ul class="nav tab-switch" role="tablist">
                      <li class="nav-item" @click="currentStep = stepName"  v-for="(stepName, st) in steps" :key="st" >
                        <a :class="(currentStep==stepName)?'nav-link active':'nav-link'" data-toggle="pill" role="tab" aria-controls="user-profile-info">{{stepName}}</a>
                      </li>
                    </ul>
                    <div class="row">
                      <div class="col-md-12">
                        <div  class="tab-content tab-body" id="profile-log-switch">
                          <div v-if="currentStep == 'Registration'" class="tab-pane fade show active pr-3" id="user-profile-info" role="tabpanel" aria-labelledby="user-profile-info-tab">
                            <table class="table table-borderless w-100 mt-4">
                              <tbody><tr>
                                <td><strong>First Name :</strong> {{form.firstName}}</td>
                                <td><strong>Date of Birth :</strong> {{form.dateOfBirth | date}}</td>
                              </tr>
                              <tr>
                                <td><strong>Middle Name :</strong> {{form.middleName}}</td>
                                <td><strong>Gender :</strong> {{form.gender}}</td>
                              </tr>
                              <tr>
                                <td><strong>Last Name :</strong> {{form.lastName}}</td>
                                <td><strong>Phone :</strong> {{form.phoneNumber}}</td>
                              </tr>
                            </tbody>
                            </table>
                            <div class="row mt-5 pb-4 border-bottom">
                              <div class="col-12">
                                <div class="d-flex align-items-start pb-3 border-bottom">
                                    <span style="font-size:7em" class="icon-location-pin"></span>
                                  <div class="wrapper pl-4">
                                    <p class="font-weight-bold mb-0">Contact Information</p>
                                    <strong>Physical Address :</strong> {{form.physicalAddress}}<br>
                                    <strong>Country :</strong> {{form.country}}<br>
                                    <strong>City :</strong> {{form.city}}<br>
                                    <strong>State :</strong> {{form.state}}<br>
                                    <strong>Postal Code :</strong> {{form.postalCode}}<br>
                                    <strong>Country of Origin :</strong> {{form.homeCountry}}<br>
                                    <strong>Suite / Apt Number :</strong> {{form.suiteNumber}}<br>
                                  </div>
                                </div>
                              </div>
                            </div>
                          </div>
                        </div>
                        <div v-if="currentStep == 'Spouse Information'" class="tab-content tab-body" id="profile-log-switch">
                          <div class="tab-pane fade show active pr-3" id="user-profile-info" role="tabpanel" aria-labelledby="user-profile-info-tab">
                            <table class="table table-borderless w-100 mt-4">
                              <tbody><tr>
                                <td><strong>First Name :</strong> {{form.spouse.firstName}}</td>
                                <td><strong>Date of Birth :</strong> {{form.spouse.dateOfBirth | date}}</td>
                              </tr>
                              <tr>
                                <td><strong>Middle Name :</strong> {{form.spouse.middleName}}</td>
                                <td><strong>Gender :</strong> {{form.spouse.gender}}</td>
                              </tr>
                              <tr>
                                <td><strong>Last Name :</strong> {{form.spouse.lastName}}</td>
                                <td><strong>Phone :</strong> {{form.spouse.phoneNumber}}</td>
                              </tr>
                            </tbody>
                            </table>
                            <div class="row mt-5 pb-4 border-bottom">
                              <div class="col-12">
                                <div class="d-flex align-items-start pb-3 border-bottom">
                                    <span style="font-size:7em" class="icon-location-pin"></span>
                                  <div class="wrapper pl-4">
                                    <p class="font-weight-bold mb-0">Contact Information</p>
                                    <strong>Physical Address :</strong> {{form.spouse.physicalAddress}}<br>
                                    <strong>Country :</strong> {{form.spouse.country}}<br>
                                    <strong>City :</strong> {{form.spouse.city}}<br>
                                    <strong>State :</strong> {{form.spouse.state}}<br>
                                    <strong>Postal Code :</strong> {{form.spouse.postalCode}}<br>
                                    <strong>Country of Origin :</strong> {{form.spouse.homeCountry}}<br>
                                    <strong>Suite / Apt Number :</strong> {{form.spouse.suiteNumber}}<br>
                                  </div>
                                </div>
                              </div>
                            </div>
                          </div>
                        </div>
                        <div v-if="currentStep == 'Next of Kin'" class="tab-pane fade show active" id="user-profile-activity" role="tabpanel" aria-labelledby="user-profile-activity-tab">
                            <div class="horizontal-timeline">
                              <section class="time-frame">
                                <div class="event">
                                  <p class="event-text">Full name</p>
                                  <div class="event-alert">{{form.kin.fullname}}</div>
                                </div>
                                <div class="event">
                                  <p class="event-text">Address</p>
                                  <div class="event-alert">{{form.kin.address}}</div>
                                </div>
                              </section>
                            </div>
                          </div>
                        <div v-if="currentStep == 'Child Information'" class="tab-pane fade show active" id="user-profile-activity" role="tabpanel" aria-labelledby="user-profile-activity-tab">
                                  <div class="card">
								<div class="card-body">
									<div class="table-responsive">
                    <table class="table">
                      <thead>
                        <tr>
                          <th class="pt-1 pl-0">
                            Name
                          </th>
                          <th class="pt-1">
                            Gender
                          </th>
                          <th class="pt-1">
                            Date Of Birth
                          </th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr v-for="(child, ch) in form.children" :key="ch">
                          <td class="py-1 pl-0">
                            <div class="d-flex align-items-center">
                              <div class="ml-3">
                                <p class="mb-2">{{ch+1}}. {{child.firstName + ' ' + child.middleName + ' ' + child.lastName }}</p>
                              </div>
                            </div>
                          </td>
                          <td>
                            <label class="badge badge-success">{{child.gender }}</label>
                          </td>
                          <td>
                            {{child.dateOfBirth | date}}
                          </td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
								</div>
							</div>
                          </div>
                        <div v-if="currentStep == 'Payment Plan'" class="tab-pane fade show active" id="user-profile-activity" role="tabpanel" aria-labelledby="user-profile-activity-tab">
                            <div class="horizontal-timeline">
                              <section class="time-frame">
                                <div class="event">
                                  <p class="event-text"> $ {{form.plan.value}} {{form.plan.frequency}}</p>
                                </div>
                              </section>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
 </template>
<script>
import firebase from 'firebase'
import { database } from '../../store/settings.module'
const usersRef = database.ref('users')
export default {
  data () {
    return {
      id: 0,
      form: {
        firstName: '',
        children: [],
        status: 0
      },
      title: '',
      submitting: false,
      detail: false,
      currentStep: 'Registration',
      category: '',
      filling: false,
      step: 0,
      steps: []
    }
  },
  computed: {
    pageTitle () {
      if (this.detail) {
        return `Subscribes to the ${this.category} subscription`
      } else if (this.filling) {
        return `Confirm your details first before proceeding to pay`
      } else {
        return `You chose the ${this.category} subscription`
      }
    },
    currentUser () {
      return JSON.parse(localStorage.getItem('user'))
    }
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      const { path } = this.$route
      if (path.includes('profile')) {
        const user = JSON.parse(localStorage.getItem('user'))
        if (user.plan) {
          this.generateSteps(user.plan)
        }
        firebase.database().ref('subscriptions').child(user.uid).on('value', (snapshot) => {
          this.form = snapshot.val()
          this.category = this.$categories.categoryDisplayName(this.form.category)
        })
      } else if (path.includes('details')) {
        this.detail = true
        firebase.database().ref('subscriptions').child(this.$route.params.id).on('value', (snapshot) => {
          this.form = snapshot.val()
          this.category = this.$categories.categoryDisplayName(this.form.category)
          this.generateSteps(this.form.category)
        })
      } else {
        this.form = JSON.parse(localStorage.getItem('form'))
        this.category = this.$categories.categoryDisplayName(this.form.category)
        this.filling = true
        this.generateSteps(this.form.category)
      }
    },
    addSubscription () {
      this.submitting = true
      this.form = JSON.parse(localStorage.getItem('form'))
      firebase.database().ref('subscriptions/' + this.form.user).set(this.form)
      usersRef.child(this.form.user).on('value', (snapshot) => {
        let user = snapshot.val()
        user.plan = this.form.category
        user.payment = this.form.plan
        user.dateAdded = new Date(Date.now()).toISOString()
        user.number = (Math.random() + ' ').substring(2, 10) + (Math.random() + ' ').substring(2, 10)
        usersRef.child(this.form.user).set(user)
      })
      let userData = JSON.parse(localStorage.getItem('user'))
      userData.plan = this.form.category
      localStorage.setItem('user', JSON.stringify(userData))
    },
    generateSteps (category) {
      const steps = ['Registration']
      switch (category) {
        case 'couple':
          steps.push('Spouse Information')
          break
        case 'single-parent':
          steps.push('Child Information')
          break
        case 'family':
          steps.push('Spouse Information')
          steps.push('Child Information')
          break
        default:
          break
      }
      steps.push('Next of Kin')
      steps.push('Payment Plan')
      this.steps = steps
    },
    payNow () {
      this.addSubscription()
      this.$router.push({ name: 'add-payment' })
    },
    subscribeNow () {
      if (this.filling) {
        this.addSubscription()
      }
      this.$router.push({ name: 'subscribe' })
    },
    back () {
      this.$router.push({
        name: 'fill-form',
        params: { category: this.form.category }
      })
    }
  }
}
</script>


