<template>
 <nav class="navbar horizontal-layout col-lg-12 col-12 p-0">
      <div class="nav-top flex-grow-1">
        <div class="container d-flex flex-row h-100 align-items-center">
          <div class="text-center navbar-brand-wrapper d-flex align-items-center">
            <a class="navbar-brand brand-logo" ><img src="static/images/logo.png" alt="logo"/></a>
            <a class="navbar-brand brand-logo-mini"><img src="static/images/logo.png" alt="logo"/></a>
          </div>
          <div class="navbar-menu-wrapper d-flex align-items-center justify-content-between flex-grow-1">
            <ul class="navbar-nav navbar-nav-right mr-0 ml-auto">
              <li class="nav-item nav-profile dropdown">
                <a class="nav-link dropdown-toggle" href="#" data-toggle="dropdown" id="profileDropdown">
                  <span class="nav-profile-name">{{currentUser.name}}</span>
                </a>
                <div class="dropdown-menu dropdown-menu-right navbar-dropdown" aria-labelledby="profileDropdown">
                  <a v-if="currentUser.type == 'admin'" @click="terms" class="dropdown-item">
                    <i class="icon-book text-primary mr-2"></i>
                    Terms and Conditions
                  </a>
                  <a @click="logout" class="dropdown-item">
                    <i class="icon-logout text-primary mr-2"></i>
                    Logout
                  </a>
                </div>
              </li>
            </ul>
            <button class="navbar-toggler align-self-center" type="button" data-toggle="minimize">
              <span class="icon-menu"></span>
            </button>
          </div>
        </div>
      </div>
      <side-bar type="sidebar" :sidebar-links="links()"></side-bar>
    </nav>
 </template>
<script>
import Breadcrumbs from '@/components/Layout/Breadcrumbs'
import firebase from 'firebase'

export default {
  components: {
    Breadcrumbs
  },
  computed: {
    currentUser () {
      return JSON.parse(localStorage.getItem('user'))
    },
    settings () {
      var set = JSON.parse(localStorage.getItem('settings'))
      if (!set) {
        this.$router.push({
          name: 'setup-account'
        })
      }
      return set
    }
  },
  data () {
    return {
      activeNotifications: false,
      searchTerm: ''
    }
  },
  methods: {
    toggleNotificationDropDown () {
      this.activeNotifications = !this.activeNotifications
    },
    closeDropDown () {
      this.activeNotifications = false
    },
    toggleSidebar () {
      this.$sidebar.displaySidebar(!this.$sidebar.showSidebar)
    },
    to (path) {
      this.$router.push({
        name: path
      })
    },
    logout () {
      firebase.auth().signOut().then(() => {
        this.$toastr('success', 'You have been logged out')
        localStorage.setItem('user', null)
        localStorage.setItem('form', null)
        window.location.href = '#/login'
        // location.reload()
      })
    },
    terms () {
      this.$router.push({ name: 'edit-terms' })
    },
    links () {
      var user = JSON.parse(localStorage.getItem('user'))
      if (user) {
        return this.$sidebar.getLinks(user.type)
      }
      return []
    }
  }
}
</script>
<style>
form.navbar-search-form {
  display: inline-block;
  color: #000;
}
.ig {
  border: 2px solid #ddd;
}

</style>



