<template>
  <div class='card'>
    <div class='card-header'>
      <div class="title" v-if="link == ''">
        <i class='icon-refresh pulse'/>
        {{pageTitle}}
      </div>
      <a v-if="link != ''" :href="link" class="btn btn-success pull-right">Click here to authorise the subscription</a>
    </div>
  </div>
</template>
<script>
import firebase from 'firebase'
export default {
  data () {
    return {
      link: '',
      pageTitle: 'Getting your plan ready ... ',
      accessToken: '',
      loading: false,
      form: {}
    }
  },
  created () {
    localStorage.setItem('planOnline', null)
    localStorage.setItem('billingAgreement', null)
    const user = JSON.parse(localStorage.getItem('user'))
    firebase.database().ref('subscriptions').child(user.uid).on('value', (snapshot) => {
      this.form = snapshot.val()
      this.getAccessToken()
    })
  },
  methods: {
    getAccessToken () {
      this.pageTitle = 'Getting your plan ready ...'
      let data = 'grant_type=client_credentials'
      this.$http.post('https://api.sandbox.paypal.com/v1/oauth2/token/', data,
        {
          headers: {
            'Accept': 'application/json',
            'Accept-Language': 'en_US',
            'Authorization': 'Basic ' + btoa(window.clientId + ':' + window.secret)
          }
        })
        .then((response) => {
          this.accessToken = response.data.access_token
          this.createPlan(this.form)
        })
        .catch((error) => {
          // An error happened.
          console.log(error)
        })
    },
    createPlan (form) {
      this.pageTitle = 'Creating your plan ...'
      let frequency = 'YEAR'
      if (form.plan.frequency === 'Monthly') {
        frequency = 'MONTH'
      }
      let billingPlan = {
        name: form.category + ' plan Payment Definition',
        description: form.category + ' plan Payment Definition.',
        type: 'INFINITE',
        payment_definitions: [
          {
            name: 'Regular payment definition',
            type: 'REGULAR',
            frequency: frequency,
            frequency_interval: '1',
            amount: {
              value: form.plan.value.toString(),
              currency: 'USD'
            },
            cycles: null,
            charge_models: []
          }
        ],
        merchant_preferences: {
          return_url: window.hostUrl + '#/subscription-execute/',
          cancel_url: window.hostUrl + '#/subcription-cancel',
          auto_bill_amount: 'YES',
          initial_fail_amount_action: 'CONTINUE',
          max_fail_attempts: '0'
        }
      }
      this.$http.post('https://api.sandbox.paypal.com/v1/payments/billing-plans/', billingPlan,
        {
          headers: {
            'Content-type': 'application/json',
            'Authorization': 'Bearer ' + this.accessToken
          }
        })
        .then((response) => {
          let plan = response.data
          localStorage.setItem('planOnline', JSON.stringify(plan))
          this.pageTitle = 'Plan created ...'
          this.activatePlan()
        })
        .catch((error) => {
          // An error happened.
          console.log(error)
        })
    },
    activatePlan () {
      this.pageTitle = 'Activating your plan ...'
      let plan = JSON.parse(localStorage.getItem('planOnline'))
      let state = [
        {
          op: 'replace',
          path: '/',
          value: {
            state: 'ACTIVE'
          }
        }
      ]
      this.$http.patch('https://api.sandbox.paypal.com/v1/payments/billing-plans/' + plan.id, state,
        {
          headers: {
            'Content-type': 'application/json',
            'Authorization': 'Bearer ' + this.accessToken
          }
        })
        .then((response) => {
          plan.state = 'ACTIVE'
          localStorage.setItem('planOnline', JSON.stringify(plan))
          this.pageTitle = 'Plan activated ...'
          this.createAgreement()
        })
        .catch((error) => {
          // An error happened.
          console.log(error)
        })
    },
    createAgreement () {
      var today = new Date()
      var tomorrow = new Date()
      tomorrow.setDate(today.getDate() + 1)
      this.pageTitle = 'Creating your subscription agreement ...'
      let plan = JSON.parse(localStorage.getItem('planOnline'))
      if (plan.state === 'CREATED') {
        this.activatePlan()
      }
      let agreement = {
        name: 'Awa Subscription',
        description: 'Awa Subscription',
        start_date: tomorrow,
        plan: {
          id: plan.id
        },
        payer: {
          payment_method: 'paypal'
        }
      }
      this.$http.post('https://api.sandbox.paypal.com/v1/payments/billing-agreements/', agreement,
        {
          headers: {
            'Content-type': 'application/json',
            'Authorization': 'Bearer ' + this.accessToken
          }
        })
        .then((response) => {
          localStorage.setItem('billingAgreement', JSON.stringify(response.data.links))
          this.link = response.data.links[0].href
          this.pageTitle = 'Your agreement is ready ...'
          this.$toastr('success', 'Ready')
        })
        .catch((error) => {
          // An error happened.
          this.pageTitle = 'Your agreement could not be completed. We will get back to you with the details'
          console.log(error)
        })
    }
  }
}
</script>
<style></style>



