<template>
  <div class='card'>
    <div class='card-header'>
      <h4 class='title'>{{pageTitle}}</h4>
    </div>
    <div class='card-body'>
      <PayPal
        :amount='detail.amount'
        currency='USD'
        env='sandbox'
        :invoice-number='invoiceNumber'
        :items='detail.subscription'
        :button-style="myStyle"
        v-on:payment-authorized="paymentAuthorized"
        v-on:payment-completed="paymentCompleted"
        v-on:payment-cancelled="paymentCancelled"
        notify-url='https://awa-insurance.firebaseio.com/paypal.json'
        :client='paypal'>
      </PayPal>
    </div>
  </div>
</template>
<script>
import PayPal from 'vue-paypal-checkout'

export default {
  data () {
    return {
      invoiceNumber: 0,
      loading: false,
      submitting: false,
      detail: {},
      paypal: {
        sandbox:
          'AReVs0yJ0E9PAUMDQ5f6t1U20keya7HD6Yc-skQN8txbE4guj05uFnG6IsvjBJ0ovPBWJFpRl-YRi6bK',
        production:
          'AReVs0yJ0E9PAUMDQ5f6t1U20keya7HD6Yc-skQN8txbE4guj05uFnG6IsvjBJ0ovPBWJFpRl-YRi6bK'
      },
      subscription: [],
      experienceOptions: {
        input_fields: {
          no_shipping: 1
        }
      },
      myStyle: {
        label: 'pay',
        size: 'responsive',
        shape: 'pill',
        color: 'blue'
      }
    }
  },
  components: {
    PayPal
  },
  computed: {
    pageTitle () {
      return 'Make payment'
    },
    buttonTitle () {
      return 'Add New'
    }
  },
  created () {
    this.generateInvoiceNumber()
    this.generateInvoiceDetails()
  },
  methods: {
    paymentAuthorized: function (data) {
      console.log('paymentAuthorized' + JSON.stringify(data))
    },
    paymentCompleted: function (data) {
      this.$toastr('success', 'Payment received. We will get back to you soon.')
      localStorage.setItem('form', null)
      this.$router.push({ name: 'overview' })
    },
    paymentCancelled: function (data) {
      this.$toastr('error', 'Your payment did not go through, please try again')
      this.generateInvoiceNumber()
    },
    generateInvoiceNumber () {
      let user = JSON.parse(localStorage.getItem('user'))
      let uid = user.uid
      this.invoiceNumber = uid + '-' + new Date(Date.now()).toISOString()
    },
    generateInvoiceDetails () {
      let form = JSON.parse(localStorage.getItem('form'))
      let categoryDetails = this.$categories.categoryDetails(form.category)
      this.detail.amount = form.plan.value.toString()
      let sub = {
        name: categoryDetails.name,
        description: categoryDetails.description,
        quantity: 1,
        price: form.plan.value,
        currency: 'USD'
      }
      this.detail.subscription = []
      this.detail.subscription.push(sub)
    }
  }
}
</script>
<style></style>



