<template>
    <div>
        <div v-if="plan" class='row'>
            <div class='col-lg-12 grid-margin stretch-card'>
                <div class='card card-weather'>
                    <div class='card-body'>
                        <div class='weather-date-location'>
                            <h3>Hello, {{currentUser.name}}</h3>
                            <p class='text-gray'>
                                <span class='weather-date'>Here is your membership card </span>
                            </p>
                        </div>
                        <membership-card :joined="dateAdded" :plan="plan" :number="number" :name="currentUser.name" ></membership-card>

                    </div>
                </div>
            </div>
        </div>
        <div v-if='!plan' class='row'>

        <div class=" card col-md-12">
          <br>
          <div class="card-body ">
            <h4>Hello, {{currentUser.name}}</h4>
            <div class="dropdown">
              <button class="btn btn-outline-primary dropdown-toggle" type="button" id="dropdownMenuOutlineButton1" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
              Please choose your category
              </button>
              <div class="dropdown-menu" aria-labelledby="dropdownMenuOutlineButton1" x-placement="bottom-start" style="position: absolute; transform: translate3d(0px, 33px, 0px); top: 0px; left: 0px; will-change: transform;">
              <a v-for='(category, sI) in categories' :key="sI+'categories'" @click='selectCategory(category.name)' class="dropdown-item" href="#">{{category.display}}</a>
              </div>
            </div>
          </div>
        </div>
        </div>
  </div>
</template>
<script>
import { database } from '../../store/settings.module'
import MembershipCard from './MembershipCard.vue'
import _ from 'lodash'
const usersRef = database.ref('users')

export default {
  components: {
    MembershipCard
  },
  data () {
    return {
      categories: [],
      dateAdded: [],
      plan: null,
      joined: '2018-11-17',
      number: ['1234', '5678', '9876', '4567']
    }
  },
  computed: {
    currentUser () {
      return JSON.parse(localStorage.getItem('user'))
    }
  },
  created () {
    this.fetch()
    this.$categories.categoryItems.forEach(element => {
      element.count = 0
      this.categories.push(element)
    })
  },
  methods: {
    selectCategory (category) {
      const categoryDetails = this.$categories.categoryDetails(category)
      this.$swal({
        title:
          'You have selected the ' + categoryDetails.display + ' subscription',
        type: 'info',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes, continue!'
      }).then(result => {
        if (result.value) {
          this.$router.push({
            name: 'terms-and-conditions',
            params: { category: categoryDetails.name }
          })
        }
      })
    },
    fetch () {
      let userData = JSON.parse(localStorage.getItem('user'))
      usersRef.child(userData.uid).on('value', snapshot => {
        let user = snapshot.val()
        if (user.plan) {
          userData.plan = user.plan
          this.plan = 'Plan: ' + _.capitalize(userData.plan)
        }
        if (user.number) {
          this.number = user.number.match(/[\s\S]{1,4}/g) || []
        }
        if (user.dateAdded) {
          this.dateAdded = user.dateAdded.split('-')
        }
        localStorage.setItem('user', null)
        localStorage.setItem('user', JSON.stringify(userData))
      })
    }
  }
}
</script>

<style>
.card .numbers {
  font-size: 2em;
  text-align: right;
}
p {
  font-size: 16px;
  line-height: 1.4em;
}

.card .icon-big {
  font-size: 2.5em;
  min-height: 64px;
}
.stats {
  font-size: 1em;
  font-weight: 100;
}
.user-card2 .risk-rate span {
  width: 130px;
  height: 130px;
  border-radius: 50%;
  border: 6px dashed #d6d6d6;
  border-top-color: #9ccc65;
  border-bottom-color: transparent;
  padding: 45px;
  display: block;
  position: relative;
}
.user-card2 .risk-rate {
  display: inline-block;
  margin: 0 auto;
}
.b-t-default {
  border-top: 1px solid #d6d6d6;
}
.b-r-default {
  border-right: 1px solid #d6d6d6;
}
.user-card2 .risk-rate span:after {
  content: "";
  width: 90px;
  height: 90px;
  background-color: rgba(156, 204, 101, 0.5);
  border-radius: 50%;
  position: absolute;
  top: 15px;
  left: 15px;
  z-index: 1;
}
</style>



