<template>
  <div class="nav-bottom">
    <div class="container">
      <ul class="nav page-navigation">
        <li v-for="(link, index)  in sidebarLinks" :key="link.name + index" class="nav-item">
          <router-link class="nav-link" active-class="active" exact :to="link.path">
          <span><i :class="'link-icon ' + link.icon"></i></span> 
          <span class="menu-title">{{link.name}}</span>
          </router-link>
        </li>
      </ul>
    </div>
  </div>
 </template>
<script>
  export default {
    props: {
      type: {
        type: String,
        default: 'sidebar',
        validator: (value) => {
          let acceptedValues = ['sidebar', 'navbar']
          return acceptedValues.indexOf(value) !== -1
        }
      },
      sidebarLinks: {
        type: Array,
        default: () => []
      }
    },
    computed: {
    },
    data () {
      return {
      }
    },
    methods: {
    },
    mounted () {
    },
    watch: {
    }
  }
</script>



