<template>

   <div class="container-scroller">
    <div class="container-fluid page-body-wrapper full-page-wrapper">
      <div class="content-wrapper auth p-0 theme-two">
        <div class="row d-flex align-items-stretch">
          <div class="col-md-4 banner-section d-none d-md-flex align-items-stretch justify-content-center">
            <div class="slide-content bg-1">
            </div>
          </div>
          <div class="col-12 col-md-8 h-100 bg-white">
            <div class="auto-form-wrapper d-flex align-items-center justify-content-center flex-column">
              <div class="nav-get-started">
                <p>Don't have an account?</p>
                <router-link class="btn get-started-btn" to="/sign-up">Sign up here</router-link>
              </div>
              <form autocomplete="off" action="#">
                 <div class="text-center">
                    <img src="static/images/logo.png" alt="logo.png">
                </div>
                <h3 class="mr-auto">&nbsp;</h3>
                <p class="mb-5 mr-auto">Log In.</p>
                <div class="form-group">
                  <div class="input-group">
                    <div class="input-group-prepend">
                      <span class="input-group-text"><i class="icon-user"></i></span>
                    </div>
                    <input v-model='credentials.email' autocomplete="off" type="text" class="form-control" placeholder="Email Address">
                  </div>
                </div>
                <div class="form-group">
                  <div class="input-group">
                    <div class="input-group-prepend">
                      <span class="input-group-text"><i class="icon-lock"></i></span>
                    </div>
                    <input v-model='credentials.password' autocomplete="off" type="password" class="form-control" placeholder="Password">
                  </div>
                </div>
                <div class="form-group">
                  <submit-button 
                  v-on:submit='submit' 
                  :loading='submitting' 
                  title="Login" icon='icon-key' 
                  class="btn btn-primary submit-btn"></submit-button>
                </div>
                <div class="wrapper mt-5 text-gray">
                  <p class="footer-text">Copyright © 2018. All rights reserved.</p>
                  <!-- <ul class="auth-footer text-gray">
                    <li><a href="#">Terms & Conditions</a></li>
                    <li><a href="#">Cookie Policy</a></li>
                  </ul> -->
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import firebase from 'firebase'
import { database } from '../../store/settings.module'
const usersRef = database.ref('users')

export default {
  name: 'RwvLogin',
  data () {
    return {
      submitting: false,
      credentials: {
        email: '',
        password: ''
      },
      user: {}
    }
  },
  methods: {
    submit () {
      localStorage.setItem('user', null)
      this.submitting = true
      firebase.auth().signInWithEmailAndPassword(this.credentials.email, this.credentials.password).then(
        (result) => {
          this.setUser(result)
          this.submitting = false
          this.$toastr('success', 'Login Successful')
        },
        (err) => {
          this.$toastr('error', 'Ooops. ' + err.message)
          this.submitting = false
        }
      )
    },
    setUser (result) {
      usersRef.child(result.user.uid).on('value', (snapshot) => {
        this.user = snapshot.val()
        var data = {
          name: result.user.displayName || this.user.firstName + ' ' + this.user.lastName,
          type: this.user.type,
          uid: result.user.uid,
          plan: this.user.plan
        }
        localStorage.setItem('user', null)
        localStorage.setItem('user', JSON.stringify(data))
        this.$router.push({name: 'overview'})
      })
    }
  },
  computed: {
    // ...mapState({
    //   errors: state => state.auth.errors
    // })
  }
}
</script>



