<template>
    <div>
      {{checkSpouse}} 
        <h5 v-if="!spouse">Applicant Information</h5><br>
        <h5 v-if="spouse">Spouse Information</h5>
      <div class="row">
        <div class="col-md-4">
          <fg-input type="text" @input="store" autocomplete='off' label="First Name *" place-holder="First Name" v-model="form.firstName" />
        </div>
        <div class="col-md-4">
          <fg-input type="text" @input="store" autocomplete='off' label="Middle Name" place-holder="Middle Name" v-model="form.middleName" />
        </div>
        <div class="col-md-4">
          <fg-input type="text" @input="store" autocomplete='off' label="Last Name *" place-holder="Last Name" v-model="form.lastName" />
        </div>
      </div>
      <div class="row">
        <div class="col-md-6">
          <div class="form-group">
              <label>Date of Birth *</label>
              <datetime type="date" @input="checkAge" autocomplete='off' input-class="form-control" v-model="form.dateOfBirth"></datetime>
          </div>        
        </div>
        <div class="col-md-6">
          <div class="form-group">
            <label>Gender *</label>
            <select v-model="form.gender" @change="store" class="form-control">
              <option value="Male">Male</option>
              <option value="Female">Female</option>
            </select>
          </div>
        </div>
      </div>
        <h5>
        <br>
          Contact Information</h5>
      <div class="row">
        <div class="col-md-6">
        <div class="form-group">
            <label>
                Phone Number * 
            </label>
            <vue-tel-input @onInput="onInput" :preferredCountries="['us', 'gb']" class="phone has-error" @input="store" autocomplete='off' v-model="form.phoneNumber"></vue-tel-input>
        </div>       
        </div>
        <div class="col-md-6">
          <div class="form-group">
            <label>Country *</label>
            <select v-model="form.country" @change="store" class="form-control">
              <option v-for="(country, count) in countries" :key="count + 'current'" >{{country}}</option>
            </select>
          </div>
        </div>

        <div class="col-md-8">
          <fg-input type="text" @input="store" autocomplete='off' place-holder="Physical Address" label="Physical Address *" v-model="form.physicalAddress" />
        </div>
        <div class="col-md-4">
          <fg-input type="text" @input="store" autocomplete='off' place-holder="Postal Code" label="Postal Code" v-model="form.postalCode" />
        </div>
        <div class="col-md-4">
          <fg-input type="text" @input="store" autocomplete='off' place-holder="Province / State" label="Province / State *" v-model="form.state" />
        </div>
        <div class="col-md-4">
          <fg-input type="text" @input="store" autocomplete='off' place-holder="City" label="City *" v-model="form.city" />
        </div>
        <div class="col-md-4">
          <div class="form-group">
            <label>Country of Origin *</label>
            <select v-model="form.homeCountry" @change="store" class="form-control">
              <option v-for="(country, count) in countries" :key="count + 'origin'" >{{country}}</option>
            </select>
          </div>        </div>
        <div class="col-md-4">
          <fg-input type="text" @input="store" autocomplete='off' place-holder="Suite / Apt Number" label="Suite / Apt Number" v-model="form.suiteNumber" />
        </div>
      </div>
    </div>
</template>
<script>
export default {
  props: {
    spouse: Boolean
  },
  data () {
    return {
      form: {},
      spouseData: {},
      countries: this.$categories.countries
    }
  },
  computed: {
    checkSpouse () {
      const formData = JSON.parse(localStorage.getItem('form'))
      if (this.spouse) {
        if (formData.spouse === null || typeof formData.spouse === 'undefined') {
          this.form = {}
        } else {
          this.form = formData.spouse
        }
      } else {
        this.form = formData
      }
    }
  },
  created () {
  },
  methods: {
    onInput ({ number, isValid, country }) {
      this.form.country = country.name
    },
    store () {
      if (this.spouse) {
        const spouse = this.form
        const formData = JSON.parse(localStorage.getItem('form'))
        formData.spouse = spouse
        localStorage.setItem('form', JSON.stringify(formData))
      } else {
        localStorage.setItem('form', JSON.stringify(this.form))
      }
    },
    checkAge () {
      if (!this.form.dateOfBirth) return
      let age = this.$returnAge(this.form.dateOfBirth)
      if (age < 18) {
        this.$toastr('error', 'The age set is below the allowed age')
        this.form.dateOfBirth = null
      } else if (age > 75) {
        this.$toastr('error', 'The age set is above the allowed age')
        this.form.dateOfBirth = null
      }
      this.store()
    }
  }
}
</script>
<style>
.phone{
  position:relative;z-index:999;
}
</style>




